<script>
import Level from "./components/Level.vue";
import bus from "./components/bus.js";
import Sliper from "./components/Sliper.vue";
import Login from "./components/Login.vue";


export default {
  name: "app",
  components: {
    Level,
    Sliper,
    Login
  },
  data: function() {
    return {
      showstartpage: true,
      showsetting: false,
      level: 3,
      start: false,
      showvideo: false,
      showvideo2: false,
      showgame1: false,
      showgame3: false,
      game1done: false,
      card1get: false,
      card2get: false,
      card3get: false,
      card4get: false,
      game2done: false,
      showturn:false,
      showturn2:false,
      showlogin:false,
      idmsg:""
    };
  },
  methods: {
    startpageevent(e) {
      if (e.target.className == "restart") {
        this.level = 1;
        this.showstartpage = false;
        document.getElementById("before1").play();
        document.getElementById("startpage").currentTime = 0;
        document.getElementById("startpage").pause();

        this.showvideo = true;

        var video = document.getElementById("myvideo");
        video.play();
        video.addEventListener("ended", this.videostop, false);
      } else if (e.target.className == "continue") {
        this.showlogin=true;
        
      } else if (e.target.className == "setting") {
        this.showsetting = true;
      } else if (e.target.className == "sliperbg") {
        this.showsetting = false;
      } /*else if (e.target.className == "memory") {
        if(this.game1done){
          this.start=true;
          this.showstartpage = false;
        }
      }*/
    },
    login(userid){
      
      let find=false;

      this.axios.get("/user.json").then(res => {
          
          for(let id in res.data){
            if(id==userid)
            find=true;
          }

          if(find){
document.getElementById("museum").play();
          document.getElementById("startpage").currentTime = 0;
          document.getElementById("startpage").pause();

          this.level = res.data[userid];
          this.showstartpage = false;
          this.showlogin=false;
          this.start = true;
          }else{
            this.idmsg="无此账号！"
          } 
        });
    },
    videostop() {
      this.showvideo = false;
      this.showturn2=true;
      this.start = true;
      var video = document.getElementById("myvideo");
      video.removeEventListener("ended", this.videostop);
      var turn2=document.getElementsByClassName("hideblackscreen tag")[0];
    turn2.addEventListener("webkitAnimationEnd", this.turnfunc);
    var startp=document.getElementById("startp");
    startp.src="/static/start/start2.png";
    },
    turnfunc(){
      this.showturn2=false;
      var turn2=document.getElementsByClassName("hideblackscreen tag")[0];
    turn2.removeEventListener("webkitAnimationEnd", this.turnfunc);
    },
    turnfunc2(){
      this.showturn=false;
      
    }
  },
  mounted() {
    document.getElementById("startpage").play();

    bus.$on("flashGame", flag => {
      //这里最好用箭头函数，不然this指向有问题
      if (flag === 1) {
        this.showgame1 = false;
        this.start = true;
        this.game1done = true;

        let item = {};
        item.name = "admit";
        item.partner = "";
        item.bagimg = "/static/prop/feature3/admit.png";
        item.text = "乐乐的大学录取通知书";
        item.show = true;
        item.largeimg = "/static/enlarge/admit.png";
        item.lw = 948;
        item.lh = 612;
        this.$children[0].addItem(item);
      } else if (flag === 2) {
        this.showgame1 = false;
        this.start = true;
      } else if (flag === 3) {
        this.start = true;
        let item = {};
        item.name = "xuegao";
        item.partner = "";
        item.bagimg = "/static/prop/feature3/xuegao.png";
        item.text = "画着雪糕的便利贴";
        item.show = true;
        this.$children[0].addItem(item);
        this.card1get = true;
        document.getElementById("notegame").style.width = 0;
        document.getElementById("notegame2").style.width = 0;
      } else if (flag === 4) {
        this.start = true;
        let item = {};
        item.name = "guodong";
        item.partner = "";
        item.bagimg = "/static/prop/feature3/guodong.png";
        item.text = "画着果冻的便利贴";
        item.show = true;
        this.$children[0].addItem(item);
        this.card2get = true;
        document.getElementById("notegame").style.width = 0;
        document.getElementById("notegame2").style.width = 0;
      } else if (flag === 5) {
        this.start = true;
        let item = {};
        item.name = "xianbei";
        item.partner = "";
        item.bagimg = "/static/prop/feature3/xianbei.png";
        item.text = "画着仙贝的便利贴";
        item.show = true;
        this.$children[0].addItem(item);
        this.card3get = true;
        document.getElementById("notegame").style.width = 0;
        document.getElementById("notegame2").style.width = 0;
      } else if (flag === 6) {
        this.start = true;
        let item = {};
        item.name = "xiaomantou";
        item.partner = "";
        item.bagimg = "/static/prop/feature3/xiaomantou.png";
        item.text = "画着旺仔小馒头的便利贴";
        item.show = true;
        this.$children[0].addItem(item);
        this.card4get = true;
        document.getElementById("notegame").style.width = 0;
        document.getElementById("notegame2").style.width = 0;
      } else if (flag === 7) {
        this.start = true;
        document.getElementById("notegame").style.width = 0;
        document.getElementById("notegame2").style.width = 0;
      } else if (flag === 8) {
        this.start = true;
        this.showgame3 = false;
        let item = {};
        item.name = "homework";
        item.partner = "";
        item.bagimg = "/static/prop/feature3/homework.png";
        item.text = "乐乐得过奖的手工课作业";
        item.show = true;
        item.largeimg = "/static/enlarge/homework.png";
        item.lw = 1146;
        item.lh = 696;
        this.$children[0].addItem(item);
        let mouse = document.getElementsByClassName("mouse2")[0];
        mouse.src = "/static/3_1/mouse.png";
        mouse.className = "mouse";
        this.$children[0].$refs.sceneThree.showmouse = false;
        this.$children[0].$refs.sceneThree.jigsawdone = false;
      } else if (flag === 9) {
        this.start = true;
        this.showgame3 = false;
      }

      if (this.card1get && this.card2get && this.card3get && this.card4get) {
        this.game2done = true;
      }
    });

    var turn=document.getElementsByClassName("turn")[0];
    turn.addEventListener("webkitAnimationEnd", this.turnfunc2);
    
  },
  created() {}
};
</script>

