<template>
  <div class="sliper">
    <p>音量：{{proportion}}</p>
    <div class="bar">
      <div class="box"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Sliper",
  data() {
    return {
      proportion: 100
    };
  },
  methods:{
  },
  mounted() {
      var that=this;
    var box = document.getElementsByClassName("box")[0];
    var bar = document.getElementsByClassName("bar")[0];
    var all = document.getElementsByClassName("sliper")[0];
    //var cha = bar.offsetWidth - box.offsetWidth;
    //console.log(cha)
    var cha=740;
    box.onmousedown = function(ev) {
      let boxL = box.offsetLeft;
      let e = ev || window.event; //兼容性
      let mouseX = e.clientX; //鼠标按下的位置
      window.onmousemove = function(ev) {
        let e = ev || window.event;
        let moveL = e.clientX - mouseX; //鼠标移动的距离
        let newL = boxL + moveL; //left值
        // 判断最大值和最小值
        if (newL < 0) {
          newL = 0;
        }
        if (newL >= cha) {
          newL = cha;
        }
        // 改变left值
        box.style.left = newL + "px";
        document.getElementById("startpage").volume=newL / cha;
        // 计算比例
        let bili = (newL / cha) * 100;
        that.proportion = Math.ceil(bili);
        return false; //取消默认事件
      };
      window.onmouseup = function() {
        window.onmousemove = false; //解绑移动事件
        return false;
      };
      return false;
    };
    // 点击音量条
    bar.onclick = function(ev) {
      let left = ev.clientX - all.offsetLeft - box.offsetWidth / 2;
      if (left < 0) {
        left = 0;
      }
      if (left >= cha) {
        left = cha;
      }
      box.style.left = left + "px";
      let bili = (left / cha) * 100;
      that.proportion = Math.ceil(bili);
      return false;
    };
  }
};
</script>
<style scoped>
.sliper {
  width: 800px;
  height: 150px;
  margin: 500px auto;
  position: relative;
}
.bar {
  width: 800px;
  height: 40px;
  border-radius: 20px;
  background: #aaa;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  cursor: pointer;
}
.box {
  width: 60px;
  height: 60px;
  background: aqua;
  position: absolute;
  bottom: 0;
  top: 0;
  right: 0;
  margin: auto 0;
  border-radius: 50%;
  cursor: pointer;
  transition: left 0.1s linear 0s;
}

p{
    font-size: 35px;
    color: white;
}
</style>


