<template>
    <div>
        <form>账号：
          <input type="text" v-model="userid" 
          maxlength="8">
          </form>
          <slot></slot>
        <button @click="login">确定</button>
        <button @click="cancel">取消</button>
    </div>
</template>
<script>
export default {
    name: "Login",
  components: {},
  props: [],
  data: function() {
    return {
      userid:""
    };
  },
  methods:{
    login(){
        this.$parent.login(this.userid);
      },
    cancel(){
      this.$parent.showlogin=false;
    }
  }
}
</script>
<style scoped>
div{
    position: absolute;
    top: 0;
  left: 0;
  width: 1920px;
  height: 1080px;
  background-color: rgba(0, 0, 0, 0.9);
  color: #fff;
  font-size: 50px;
  padding-top: 480px;
  text-align: center;
  z-index: 6;
}
button{
    background-color: #fff;
    color: black;
    font-size: 45px;
    padding: 5px;
    margin: 50px;
}
input{
  font-size: 50px;
  width: 400px;
}
</style>

