<template>
  <div class="ctn" @click="eventslisten">
    <img src="/static/1_2/circular.png" :style="{width:'642px',height:'756px'}">
    <img
      class="read"
      src="/static/1_0/read.png"
      :style="{ position: 'absolute', top: '100px', left: '1200px',
  width:'42px',height:'42px'}"
    >
    <img src="/static/museum/down2.png" class="mydown">
    <div class="moreinfo" v-show="reading">
      <h1>本店招牌：鱼籽寿司</h1>
      <h2>粒粒饱满，享受鲜活在口里爆炸的感觉！</h2>
      <p>猫咪都被馋哭了！</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Feature",
  components: {},
  props: [],
  data: function() {
    return {
      reading: false
    };
  },
  computed: {},
  methods: {
    eventslisten(e) {
      if (e.target.className == "read") {
        this.reading = true;
        document.getElementById("btn").play();
      } else if (e.target.className == "mydown") {
        this.$off();
        this.$parent.feaCircular = false;
        //document.getElementById("btn").play();
      } else if (e.target.className == "moreinfo") {
        this.reading = false;
      }
    }
  }
};
</script>

<style scoped lang="scss">
