<template>
  <div @click="change">
    <audio id="huchi" src="/static/第三关/狗呼哧呼哧.mp3" hidden="true"></audio>
    <audio id="bark" src="/static/第三关/两声狗叫.mp3" hidden="true"></audio>
    <audio id="bell" src="/static/第三关/铃铛.mp3" hidden="true"></audio>
    <audio id="birdcall" src="/static/第三关/鸟叫.mp3" hidden="true"></audio>

    <div class="container">
      <!--博物馆入口-->
      <img v-if="scene.name=='start31'" src="static/museum/entry3.png" class="roomentry">

      <!--可近景物品-->
      <inner-obj
        v-for="obj in objs"
        :key="obj.name"
        :obj="obj"
        :link="obj.link"
        :name="obj.name"
        class="entry"
      ></inner-obj>

      <!--山楂有动画-->
      <img src="/static/3_2/shanzha.png" class="shanzha" v-show="!getshanzha&&scene.name==='kitchen'">

      <!--回忆录特效-->
      <div v-for="obj in effect" :key="obj.name">
        <img :src="obj.imgsrc" class="effect1">
      </div>
      <!--博物馆手工作业掉下-->
    <img
      src="static/3_5/homeworkan.png"
      v-if="scene.name=='memorybg3'&&showhwan"
      class="homeworkan"
    >

      <!--可被作用物品，不可收集-->
      <inner-obj
        v-for="obj in objs3"
        :key="obj.name"
        :obj="obj"
        :partner="obj.partner"
        :name="obj.name"
        class="cantget"
      ></inner-obj>

      <!--可收集物品-->
      <div v-for="obj in objs2" :key="obj.name">
        <inner-obj v-if="!obj.isget" :obj="obj" @click.native="getobj2($event,obj)"></inner-obj>
      </div>

      <!--可触发对话框对象-->
      <div v-for="obj in oneSceneIns" :key="obj.name">
        <inner-obj :obj="obj" @click.native="hideDialog(obj)"></inner-obj>
      </div>
      <!--其他-->

      <!--魔术箱密码-->
      <div v-if="scene.name==='apenguinbox'">
        <p-password :num="1" class="pw1"></p-password>
        <p-password :num="2" class="pw2"></p-password>
        <p-password :num="3" class="pw3"></p-password>
      </div>
      <!--玩具箱密码-->
      <t-password v-if="scene.name==='atoybox'"></t-password>

      <!--拼图物品-->
      <div v-if="scene.name==='ajigsaw'">
        <jigsaw-obj v-for="obj in jigsawobj" :key="obj.name" :obj="obj"></jigsaw-obj>
      </div>

      <!--汪-->
      <img src="/static/3_1/wang.png" class="wang" v-show="scene.name=='livingroom'&&showwang">

      <!--老鼠富翁-->
      <img src="/static/3_1/mouse.png" class="mouse" v-show="showmouse&&scene.name=='livingroom'">

      <!--白屏-->
      <div class="whitescreen" v-if="showwhite"></div>
      <!--母亲画卷背景-->
      <canvas id="mycanvas" v-show="showmompicbg" width="1920" height="1080"></canvas>
      <!--画卷-->
      <div v-if="showmompic" class="mompic">
        <img src="static/enlarge/mompic.png">
      </div>

      <div class="fea" v-if="feaEgg">
        <img src="/static/3_4/coloregg.png" class="coloregg">
      </div>

      <!--场景切换箭头-->
      <div v-show="showarrowhead">
        <img src="/static/museum/down2.png" class="down" v-if="scene.down&&scene.name!='end12'&&scene.name!='end32'">
        <img src="/static/museum/right2.png" class="right" v-if="scene.right">
        <img src="/static/museum/right2.png" class="left" v-if="scene.left">
      </div>
      <img class="down memorydown" v-if="memorydown" src="/static/museum/down2.png">

      <!--博物馆下箭头-->
      <img src="/static/museum/down2.png" class="museumdown" v-if="scene.name=='end12'||scene.name=='end32'">
    </div>

    <!--博物馆笔-->
    <img src="static/museum/museumpen.png" v-if="scene.name=='end21'" class="museumpen">
    <img src="static/museum/pentext.png" v-if="scene.name=='end21'" class="pentext">

    <img src="/static/museum/miqi.png" class="miqimuseum" v-if="scene.name=='end12'">

    

    <!--博物馆回忆录文本-->
    <p style="position:absolute;top:800px;left:100px;" v-if="scene.name==='memorybg1'">
      虽然你不在我的回忆中，
      <br>但你却化成微风轻抚着我的脸颊
    </p>
    <p
      style="position:absolute;top:900px;left:1000px;"
      v-if="scene.name==='memorybg1'"
    >乐乐把全家福带去学校了，这里就放乐乐的画吧</p>
    <p
      style="position:absolute;top:820px;left:1000px;"
      v-if="scene.name==='memorybg2'"
    >他总有一天会知道的，什么时候告诉他呢？</p>
    <p
      style="position:absolute;top:900px;left:100px;"
      v-if="scene.name==='memorybg2'"
    >乐乐又长大一岁了，时间过得多快啊</p>
    <p style="position:absolute;top:900px;left:1300px;" v-if="scene.name==='memorybg3'">乐乐的第一份获奖大作！</p>
    <p style="position:absolute;top:900px;left:200px;" v-if="scene.name==='memorybg4'">非学无以广才，非志无以成学</p>
    <p
      style="position:absolute;top:900px;left:1050px;"
      v-if="scene.name==='memorybg4'"
    >乐乐陪伴我的时光，就是最好的礼物</p>

    <!--博物馆回忆录下箭头-->
    <img class="down mmemorydown" v-if="mmemorydown" src="/static/museum/down2.png">

    <!--对话框-->
    <div class="dialog" v-if="!hideDlg">
      <my-dialog :texts="dlgTexts"></my-dialog>
      <img v-show="showmom" src="/static/3_0/mom.png" width="324px" height="540px" style="left:0">
      <img v-show="showson" src="/static/3_0/son.png" width="390px" height="660px" style="left:0px">
      <img
        v-if="showson2"
        src="/static/museum/son1.png"
        width="360px"
        height="660px"
        style="left:0px"
      >
    </div>

    <story v-if="showstory" :type="storytype"></story>
    <!--结局选择-->
    <div class="endselect" v-if="showselect">
      <p>确定离开吗？<br><br></p>
      <div class="no">我还想再看看</div>
      <div class="yes">走吧</div>
    </div>
    <div class="endtext" v-if="showendtext">凡事有期，万物有时。乐乐，我一直都在。</div>
  </div>
</template>

<script>
import InnerObj from "./InnerObj.vue";
import MyDialog from "./MyDialog.vue";
import PPassword from "./PPassword.vue";
import TPassword from "./TPassword.vue";
import JigsawObj from "./JigsawObj.vue";
import Story from "./Story.vue";

//import bus from "./bus.js";

//import axios from "axios";

export default {
  name: "SceneTwo",
  components: { InnerObj, MyDialog, PPassword, TPassword, JigsawObj,Story },
  props: ["scene"],
  data: function() {
    return {
      //scene: this.scene,
      showarrowhead: true,
      hideDlg: true,
      dlgTexts: [],
      showmom: false,
      showson: false,
      showson2: false,
      getshanzha: false,
      showmouse: false,
      showwang: false,
      showhwan: false,
      birdget: false,
      memorydone: false,
      showpentext:false,
      showstory:false,
      storytype:1,
      showselect:false,
      showendtext:false,

      jigsawdone: false,
      showwhite: false,
      showmompicbg: false,
      showmompic: false,
      feaEgg: false,
      memorydown: false,
      mmemorydown: false,


      linkobjs: [],

      canGetObjs: [],

      cantGetObjs: [],

      instru: [],

      jigsawobjs: [],

      effects: []
    };
  },
  computed: {
    objs: function() {
      let that = this;
      return this.linkobjs.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    objs2: function() {
      let that = this;
      return this.canGetObjs.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    oneSceneIns: function() {
      let that = this;
      return this.instru.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    objs3: function() {
      let that = this;
      return this.cantGetObjs.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    jigsawobj: function() {
      let that = this;
      return this.jigsawobjs.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    effect: function() {
      let that = this;
      return this.effects.filter(function(item) {
        return item.from === that.scene.name;
      });
    }
  },
  methods: {
    change(e) {
      if (e.target.className == "up") {
        this.$parent.changescene(this.scene.up);
      } else if (
        e.target.className == "down" ||
        e.target.className == "museumdown"
      ) {
        this.$parent.changescene(this.scene.down);
        //document.getElementById("btn").play();

        if (this.scene.name === "ajigsaw") {
          let count = 0;
          for (let i = 0; i < 11; i++) {
            if (!this.jigsawobjs[i].canmove) count++;
          }
          if (count == 11) this.jigsawdone = true;
        } else if (this.scene.name === "amirror") {
          if (!this.$parent.havebag) this.$parent.havebag = true;
          for (let i = 16; i < 20; i++) {
            if (!this.canGetObjs[i].isget) {
              this.getobj2(e, this.canGetObjs[i]);
            }
          }
        }
      } else if (e.target.className == "left") {
        this.$parent.changescene(this.scene.left);
        //document.getElementById("btn").play();
      } else if (e.target.className == "right") {
        this.$parent.changescene(this.scene.right);
        //document.getElementById("btn").play();
      } else if (e.target.className == "roomentry") {
        document.getElementById("level3").play();
        document.getElementById("museum").currentTime = 0;
        document.getElementById("museum").pause();
        this.$parent.changescene("livingroom");
        this.hideDlg = false;
        this.showson2=false;
        this.showmom = true;
        this.dlgTexts = [
          "乐乐刚放假回来就约同学去看电影了，还有很多时间。",
          "我要趁乐乐没回来做什么来着？看下备忘录……"
        ];
        this.$parent.havebag = true;
      } else if (e.target.className == "entry") {
        this.$parent.changescene(e.target.getAttribute("link"));
        if (e.target.getAttribute("link") == "memorybg1") {
          if (!this.memorydone) {
            //从母亲房进入回忆录
            this.memorydown = true;
          } else {
            this.mmemorydown = true;
          }
        }
      } else if (e.target.className == "down memorydown") {
        this.memorydown = false;
        this.$parent.changescene("momroom");
        if (this.memorydone) {
          document.getElementById("momback").play();
          this.hideDlg = false;
          this.dlgTexts = ["乐乐回来了！赶紧收好回忆录！"];
        }
      } else if (e.target.className == "down mmemorydown") {
        this.mmemorydown = false;
        this.$parent.changescene("end31");
      } else if (e.target.className == "cantget") {
        let partner = e.target.getAttribute("partner");
        let name = e.target.getAttribute("name");

        //便利贴
        if (this.scene.name === "amirror") {
          let index;
          let obj = this.$parent.activeItem;
          if (obj === "xianbei") {
            index = 16;
          } else if (obj === "xiaomantou") {
            index = 17;
          } else if (obj === "xuegao") {
            index = 18;
          } else if (obj === "guodong") {
            index = 19;
          } else {
            this.$parent.activeItem = null;
            return;
          }
          let obj2 = this.canGetObjs[index];
          if (name === "xianbeic") {
            obj2.isget = false;
            obj2.x = 1164;
            obj2.y = 606;
          } else if (name === "xiaomantouc") {
            obj2.isget = false;
            obj2.x = 1164;
            obj2.y = 48;
          } else if (name === "xuegaoc") {
            obj2.isget = false;
            obj2.x = 672;
            obj2.y = 618;
          } else if (name === "guodongc") {
            obj2.isget = false;
            obj2.x = 672;
            obj2.y = 48;
          }
          this.$parent.removeItem(obj);
          if (
            this.canGetObjs[16].x == 1164 &&
            this.canGetObjs[16].y == 606 &&
            this.canGetObjs[17].x == 1164 &&
            this.canGetObjs[17].y == 48 &&
            this.canGetObjs[18].x == 672 &&
            this.canGetObjs[18].y == 618 &&
            this.canGetObjs[19].x == 672 &&
            this.canGetObjs[19].y == 48 &&
            !this.canGetObjs[16].isget&&!this.canGetObjs[17].isget&&!this.canGetObjs[18].isget&&
            !this.canGetObjs[19].isget
          ) {
            this.showarrowhead = false;
            this.showwhite = true;
            this.$parent.havebag = false;
            let that = this;
            setTimeout(function() {
              that.canGetObjs[23].from = "amirror";
              that.showmompicbg = true;
              that.draw();
            }, 2500);
          }

          return;
        }
        if (name == "catclick") {
          document.getElementById("miao").play();
        } else if (name == "bird" || name == "bird3" || name == "bird2") {
          document.getElementById("birdcall").play();
          if (this.birdget) {
            this.cantGetObjs[15].from = "bedroom";
            let that = this;
            setTimeout(function() {
              that.cantGetObjs[15].from = "";
            }, 1000);
          }
        }

        if (partner) {
          if (this.$parent.activeItem === partner) {
            this.$parent.removeItem(partner);

            if (name == "bird") {
              this.cantGetObjs[15].from = "bedroom";
              let that = this;
              setTimeout(function() {
                that.cantGetObjs[15].from = "";
                that.cantGetObjs[14].imgsrc = "/static/3_3/bird2.png";
                that.birdget = true;
              }, 500);
            } else if (name == "catclick") {
              var shanzha = document.getElementsByClassName("shanzha")[0];
              shanzha.style.top = 400 + "px";
              this.cantGetObjs[13].from = "kitchen";
            } else if (name == "dog") {
              new Image().src="/static/3_1/dog2.png";
              this.cantGetObjs[11].from = "livingroom";
              this.cantGetObjs[10].from = "";
              document.getElementById("bark").play();
              this.showwang = true;
              return;
            } else if (name == "mmarryc") {
              //回忆录

              this.cantGetObjs[19].from = "memorybg1";
              this.effects[0].from = "memorybg1";
              this.checkeffect();
            } else if (name == "mthreec") {
              this.cantGetObjs[20].from = "memorybg1";
              this.effects[1].from = "memorybg1";
              this.checkeffect();
            } else if (name == "msmallc") {
              this.cantGetObjs[21].from = "memorybg2";
              this.effects[2].from = "memorybg2";
              this.checkeffect();
            } else if (name == "millrecordc") {
              this.cantGetObjs[22].from = "memorybg2";
              this.effects[3].from = "memorybg2";
              this.checkeffect();
            } else if (name == "mmiddlec") {
              this.cantGetObjs[23].from = "memorybg2";
              this.effects[4].from = "memorybg2";
              this.checkeffect();
            } else if (name == "mhomeworkc") {
              this.cantGetObjs[24].from = "memorybg3";
              this.effects[5].from = "memorybg3";
              this.showhwan = true;
              this.checkeffect();
            } else if (name == "madmitc") {
              this.cantGetObjs[25].from = "memorybg4";
              this.effects[6].from = "memorybg4";
              this.checkeffect();
            } else if (name == "mmompicc") {
              this.cantGetObjs[26].from = "memorybg4";
              this.effects[7].from = "memorybg4";
              this.checkeffect();
            }
          } else {
            this.$parent.activeItem = null;
          }
        }
        if (name === "refri") {
          this.cantGetObjs[1].from = "kitchen";
          document.getElementById("openr").play();
          if (!this.canGetObjs[0].isget) {
            this.cantGetObjs[2].from = "kitchen";
          }
        } else if (name === "snacks") {
          this.cantGetObjs[2].from = "";
          this.getobj2(e, this.canGetObjs[0]);
          this.getobj2(e, this.canGetObjs[1]);
          this.getobj2(e, this.canGetObjs[2]);
          this.getobj2(e, this.canGetObjs[3]);
          this.getobj2(e, this.canGetObjs[4]);
          this.getobj2(e, this.canGetObjs[5]);
          this.getobj2(e, this.canGetObjs[6]);
        } else if (name === "coloregg") {
          this.feaEgg = true;
        } else if (name === "refriopen") {
          document.getElementById("closer").play();
          this.cantGetObjs[1].from = "";
        } else if (name === "dog") {
          document.getElementById("huchi").play();
        } else if (name === "dog2") {
          document.getElementById("bark").play();
          this.showwang = true;
        } else if (name === "snacksbox") {
          this.cantGetObjs[4].from = "livingroom";
          if (!this.canGetObjs[8].isget) {
            this.cantGetObjs[5].from = "livingroom";
          }
        } else if (name === "snacksinbox") {
          this.cantGetObjs[5].from = "";
          this.getobj2(e, this.canGetObjs[8]);
          this.getobj2(e, this.canGetObjs[9]);
          this.getobj2(e, this.canGetObjs[10]);
          this.getobj2(e, this.canGetObjs[26]);
        } else if (name === "snacksboxopen") {
          this.cantGetObjs[4].from = "";
          this.cantGetObjs[5].from = "";
        } else if (name === "birthpic") {
          this.cantGetObjs[9].from = "";
          this.getobj2(e, this.canGetObjs[14]);
          this.getobj2(e, this.canGetObjs[15]);
        } else if (name === "bookgame") {
          let p = this.$parent.$parent;
          if (p.game1done === false) {
            p.showgame1 = true;
            p.start = false;
          }
        } else if (name === "notegame") {
          let p = this.$parent.$parent;
          if (p.game2done === false) {
            p.start = false;
            document.getElementById("notegame").style.width = 1920 + "px";
            document.getElementById("notegame2").style.width = 1920 + "px";
          }
        } else if (name === "bookpiece") {
          this.storytype=1;
          this.showstory=true;
        } else if (name === "upstair") {
          this.storytype=3;
          this.showstory=true;
        } else if (name === "endselect") {
          this.showselect=true;
        } else if (name === "bell") {
          document.getElementById("bell").play();
          let mouse = document.getElementsByClassName("mouse")[0];
          if (!this.jigsawdone) {
            mouse.className = "mouse";
            this.showmouse = true;
            mouse.addEventListener("webkitAnimationEnd", this.mousefunc);
          } else {
            mouse.className = "mouse2";
            this.showmouse = true;
            mouse.addEventListener("webkitAnimationEnd", this.mousefunc2);
          }
        } else if (name === "jigsawclick") {
          //拼图
          if (this.$parent.activeItem === "beerbiscuit") {
            this.jigsawobjs[0].from = "ajigsaw";
            this.jigsawobjs[0].x = e.clientX;
            this.jigsawobjs[0].y = e.clientY;
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "mblack") {
            this.jigsawobjs[1].from = "ajigsaw";
            this.jigsawobjs[1].x = e.clientX;
            this.jigsawobjs[1].y = e.clientY;
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "mpink") {
            this.jigsawobjs[2].from = "ajigsaw";
            this.jigsawobjs[2].x = e.clientX;
            this.jigsawobjs[2].y = e.clientY;
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "mwhite") {
            this.jigsawobjs[3].from = "ajigsaw";
            this.jigsawobjs[3].x = e.clientX;
            this.jigsawobjs[3].y = e.clientY;
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "mimi") {
            this.jigsawobjs[4].from = "ajigsaw";
            this.jigsawobjs[4].x = e.clientX;
            this.jigsawobjs[4].y = e.clientY;
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "stonechkl") {
            this.jigsawobjs[5].from = "ajigsaw";
            this.jigsawobjs[5].x = e.clientX;
            this.jigsawobjs[5].y = e.clientY;
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "lollipop") {
            this.jigsawobjs[6].from = "ajigsaw";
            this.jigsawobjs[6].x = e.clientX;
            this.jigsawobjs[6].y = e.clientY;
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "shanzha") {
            this.jigsawobjs[7].from = "ajigsaw";
            this.jigsawobjs[7].x = e.clientX;
            this.jigsawobjs[7].y = e.clientY;
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "rabbitcandy") {
            this.jigsawobjs[8].from = "ajigsaw";
            this.jigsawobjs[8].x = e.clientX;
            this.jigsawobjs[8].y = e.clientY;
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "moguli") {
            this.jigsawobjs[9].from = "ajigsaw";
            this.jigsawobjs[9].x = e.clientX;
            this.jigsawobjs[9].y = e.clientY;
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "weihua") {
            this.jigsawobjs[10].from = "ajigsaw";
            this.$parent.removeItem(this.$parent.activeItem);
          } else {
            this.$parent.activeItem = null;
          }
        }
      } else if (e.target.className == "shanzha") {
        this.getobj2(e, this.canGetObjs[21]);
        this.getshanzha = true;
      } else if (e.target.className == "mompic") {
        this.showmompic = false;
        for (let i = 16; i < 20; i++) {
          this.canGetObjs[i].isget = true;
        }
        this.$parent.havebag = true;
        this.showarrowhead = true;
      } else if (e.target.className == "fea") {
        this.feaEgg = false;
      } else if (e.target.className == "mouse2") {
        this.$parent.$parent.showgame3 = true;
        this.$parent.$parent.start = false;
        //this.showmouse=false;
      } else if (e.target.className == "museumpen") {
        //this.showpentext=true;
        document.getElementsByClassName("pentext")[0].style.display="block";
        document.getElementsByClassName("pentext")[0].style.opacity = 1;
      } else if (e.target.className == "sp1"||e.target.className == "sp2"||e.target.className == "sp3") {
        this.showstory=false;
      } else if (e.target.className == "pentext") {
        this.storytype=2;
        this.showstory=true;
      } else if (e.target.className == "no") {
        this.showselect=false;
      } else if (e.target.className == "yes") {
        this.showselect=false;
        this.showendtext=true;
      } else if (e.target.className == "endtext") {
        this.showendtext=false;
        this.$parent.$parent.start=false;
        //
        this.$parent.$parent.showvideo2 = true;

document.getElementById("museum").currentTime = 0;
          document.getElementById("museum").pause();
        var video = document.getElementById("myvideo2");
        video.play();
        video.addEventListener("ended", this.videostop2, false);
      }
    },
videostop2(){
this.$parent.$parent.showstartpage=true;
this.$parent.$parent.showvideo2 = false;
var video = document.getElementById("myvideo2");
      video.removeEventListener("ended", this.videostop2);
},
    getobj2(event, obj) {
      event.stopPropagation();
      //event.cancelBubble = true;

      obj.isget = true;
      let item = {};
      item.name = obj.name;
      item.partner = obj.partner;
      item.bagimg = obj.bagimg;
      item.text = obj.text;
      obj.show != undefined ? (item.show = obj.show) : (item.show = true);
      if (obj.largeimg) {
        item.largeimg = obj.largeimg;
        item.lw = obj.lw;
        item.lh = obj.lh;
      }

      this.$parent.addItem(item);
      if (obj.name === "snackspic") {
        this.hideDlg = false;
        this.dlgTexts = [
          "能不能用零食做出一张还原画，跟老鼠商人换回手工课作业呢？",
          "就在客厅茶几上做吧。"
        ];
        this.showmom = true;
      } else if (obj.name === "mompic") {
        this.showmompicbg = false;
        this.showmompic = true;
        this.showwhite = false;
        this.cantGetObjs[36].from = "amirror";
        this.cantGetObjs[35].from = "";
        this.cantGetObjs[37].from = "";
      }
    },
    hideDialog(obj) {
      this.hideDlg = false;
      this.dlgTexts = obj.text;

      if (obj.name == "notesay") {
        this.showmom = false;
        //this.showson = true;
      } else {
        this.showson = false;
        this.showmom = true;
      }
      if (obj.name == "pwsay") {
        this.instru[1].from = "";
      }
    },
    mousefunc() {
      this.showmouse = false;
      let mouse = document.getElementsByClassName("mouse")[0];
      mouse.removeEventListener("webkitAnimationEnd", this.mousefunc);
    },
    mousefunc2() {
      //this.showmouse=false;
      let mouse = document.getElementsByClassName("mouse2")[0];
      mouse.src = "/static/3_1/mouse2.png";
      mouse.removeEventListener("webkitAnimationEnd", this.mousefunc2);
    },
    draw() {
      var canvas = document.getElementById("mycanvas");
      var context = canvas.getContext("2d");
      //context.fillStyle = "rgba(0,0,0,0.7)";
      var radialGradient1 = context.createRadialGradient(
        595,
        750,
        205,
        595,
        750,
        240
      );
      radialGradient1.addColorStop(0, "rgba(0, 0, 0, 0)");
      radialGradient1.addColorStop(1, "rgba(0,0,0,0.7)");
      context.fillStyle = radialGradient1;
      context.fillRect(0, 0, 1920, 1080);
    },
    checkeffect() {
      let count = 0;
      for (let i = 0; i < 8; i++) {
        if (this.effects[i].from != "") {
          count++;
        }
      }
      if (count == 8) {
        this.memorydone = true;
      }
    },
    level3done() {
      this.$parent.$parent.showturn=true;
      setTimeout(() => {
        this.$parent.changescene("end31");
      this.$parent.scenes["end21"].right = "end31";
      this.$parent.scenes["end11"].left = "end4";
      this.$parent.havebag = false;
       document.getElementById("museum").play();
        document.getElementById("level3").currentTime = 0;
        document.getElementById("level3").pause();
      }, 1500);
      
    },
    getData() {
      this.axios.get("/R3.json").then(res => {
        this.linkobjs = res.data.linkobjs;
        this.canGetObjs = res.data.canGetObjs;
        this.cantGetObjs = res.data.cantGetObjs;
        this.instru = res.data.instru;
        this.jigsawobjs = res.data.jigsawobjs;
        this.effects = res.data.effects;
      });
    }
  },
  mounted() {
    let that = this;
    var wang = document.getElementsByClassName("wang")[0];
    wang.addEventListener("webkitAnimationEnd", function() {
      that.showwang = false;
    });

    //图片预加载
    new Image().src = "static/3_1/dog2.png";
    new Image().src = "static/3_0/squareon.png";

    this.showson2 = true;
    this.hideDlg = false;
    this.dlgTexts = ["妈妈从那个时候就开始偷偷看病了吗……"];
  },
  created() {
    this.getData();
  }
};
</script>

