<template>
  <div @click.stop="clickme">
    <div :class="classObject">
      <img class="itemimg" :src="bagimg" width="240px" height="210px">
    </div>
    <div v-show="show" class="bghide"></div>
    <div :class="classinfo" v-show="show">
      <div class="close"></div>
      <img class="feature" :src="bagimg">
              <img v-if="item.largeimg" src="/static/enlarge/enlarge.png" class="enlarge">

      <div class="text">
        <p v-if="text">{{text}}</p>
      </div>
      <img v-if="name==='memorandum'" src="/static/1_0/read.png" class="readmemorandum">
      
      
    </div>
    <div class="moreinfo" v-if="reading">
      <p>完成回忆录：</p>
      <p>·乐乐的手工课作业</p>
      <p>·全家福涂鸦</p>
      <p>·结婚纪念照</p>
      <p>·乐乐的生日照</p>
      <p>·乐乐的大学录取通知书</p>
      <p>·病历</p>
      <br>
      <p>乐乐说他在镜子那里给我留了一个惊喜，是什么呢？</p>
    </div>

    <!--大图片-->
    <div v-if="enlargepic" class="enlargepic">
      <img :src="item.largeimg" :style="{width:item.lw+'px',height:item.lh+'px'}" class="enlargepicimg">
    </div>
  </div>
</template>

<script>
import bus from "./bus.js";

export default {
  name: "MybagObj",
  components: {},
  props: ["item", "activeItem"],
  data: function() {
    return {
      //activeItem: this.activeItem,
      //active: false,
      show: this.item.show,
      reading: false,
      partner: this.item.partner,
      name: this.item.name,
      bagimg: this.item.bagimg,
      text: this.item.text,
      enlargepic:false,
    };
  },
  computed: {
    classObject: function() {
      return {
        item: this.$parent.level === 1,
        activeitem: this.name == this.activeItem && this.$parent.level === 1,
        item2: this.$parent.level === 2,
        activeitem2: this.name == this.activeItem && this.$parent.level === 2,
        item3: this.$parent.level === 3,
        activeitem3: this.name == this.activeItem && this.$parent.level === 3
      };
    },
    classinfo: function() {
      return {
        info: this.$parent.level === 1,
        info2: this.$parent.level === 2,
        info3: this.$parent.level === 3
      };
    },
    tag: function() {
      return this.name == this.activeItem ? 1 : 0;
    }
  },
  methods: {
    clickme(e) {
      if (e.target.className == "itemimg") {
        if (this.tag == 0) {
          this.$parent.setActiveItem(this.name);
          //this.$emit("setActive", this.name);
          //this.activeItem=this.name;
        } else if (this.tag == 1) {
          this.show = true;
          //this.$emit("setActive", null);
          this.$parent.setActiveItem(null);
        }
      } else if (e.target.className == "close") {
        this.show = false;
      } else if (e.target.className == "readmemorandum") {
        this.reading = true;
        document.getElementById("btn").play();
      } else if (e.target.className == "moreinfo") {
        this.reading = false;
      } else if (e.target.className == "enlarge") {
        this.enlargepic = true;
      } else if (e.target.className == "enlargepic"||e.target.className == "enlargepicimg") {
        this.enlargepic = false;
      } else if (e.target.className == "feature") {
        if (this.name === "paopaot") {
          this.bagimg = "/static/prop/feature/paopaott.png";
          this.text = "只要我吹的泡泡糖够大，它就能带我飞起来!";
          this.$parent.makepaopaot = true;
          document.getElementById("paopaotang").play();
        }

        if (this.activeItem === this.partner) {
          if (this.name === "miqi" && this.$parent.makepaopaot) {
            this.bagimg = "/static/prop/feature/miqifly.png";
            this.text = "我的小飞鼠店员";
            this.$parent.getmiqifly = true;
          }else if(this.name === "miqi" && !this.$parent.makepaopaot){
            this.$parent.activeItem = null;
            return;
          }
          if (this.partner == "cut") {
            new Image().src="/static/prop/feature2/piece.png";
            this.bagimg = "/static/prop/feature2/piece.png";
            this.text = "大小形状合适的纸片";
            bus.$emit("updatestatus", "lrkcnote");
          } else if (this.partner == "purple" || this.partner == "laverbag") {
            new Image().src="/static/prop/feature2/laver.png";
            this.bagimg = "/static/prop/feature2/laver.png";
            this.text = "虽然是假紫菜，但至少能蒙混过关";
            bus.$emit("updatestatus", "laver");
          } 
          this.$parent.removeItem(this.partner);
          this.$parent.activeItem = null;
        } else {
          this.$parent.activeItem = null;
        }
      }
    }
  },
  mounted() {
  }
};
</script>

<style scoped>
.item {
  width: 252px;
  height: 222px;
  background-image: url("/static/1_0/square.png");
  margin-left: 58px;
  text-align: center;
  padding-top: 5px;
}

.activeitem {
  background-image: url("/static/1_0/squareon.png");
}

.item2 {
  width: 324px;
  height: 228px;
  background-image: url("/static/2_0/square.png");
  margin-left: -2px;
  text-align: center;
  padding-top: 5px;
}
.activeitem2 {
  background-image: url("/static/2_0/squareon.png");
}
.item3 {
  width: 264px;
  height: 234px;
  background-image: url("/static/3_0/square.png");
  margin-left: 45px;
  text-align: center;
  padding-top: 5px;
}

.activeitem3 {
  background-image: url("/static/3_0/squareon.png");
}
.info {
  position: fixed;
  width: 732px;
  height: 702px;
  left: 594px;
  top: 40px;
  background-image: url("/static/1_0/featurectn.png");
}
.info2 {
  position: fixed;
  width: 732px;
  height: 702px;
  left: 594px;
  top: 40px;
  background-image: url("/static/2_0/featurectn.png");
  color: white;
}
.info3 {
  position: fixed;
  width: 732px;
  height: 702px;
  left: 594px;
  top: 40px;
  background-image: url("/static/3_0/featurectn.png");
  color: bisque;
}
.info,
.info2,
.info3 {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}
.close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 48px;
  height: 48px;
  cursor: pointer;
}
.feature {
  width: 480px;
  height: 420px;
}
.text {
  font-size: 40px;
  font-family: "myfont";
  line-height: 48px;
  text-align: start;
  margin-left: 70px;
  margin-right: 70px;
}
.readmemorandum {
  position: absolute;
  top: 530px;
  right: 200px;
  width: 42px;
  height: 42px;
  cursor: pointer;
}
.moreinfo {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 8;
  background-color: rgba(0, 0, 0, 0.8);
  padding-top: 250px;
  padding-left: 600px;
  cursor: pointer;
}

.moreinfo p {
  color: white;
  font-family: "myfont";
  font-weight: normal;
  font-size: 40px;
  line-height: 60px;
}
.enlarge{
  width: 42px;
  height: 42px;
  position: relative;
  top:-40px;
}
.enlargepic{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 8;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.bghide{
  position: fixed;
  top: 0;
  left: 0;
  width: 1920px;
  height: 810px;
  background-color: rgba(0,0,0,0);
}
</style>