<template>
  <div>
    <img
      v-for="bg in scenes"
      :key="bg.name"
      v-show="bg.name==nowscene.name"
      class="bgimg"
      :src="bg.imgsrc"
    >



    <scene-one :scene="nowscene" v-if="level===1">
      <!--可近景物品
      <template slot="entry" slot-scope="slotProps">
      <scene-entry :obj="slotProps.obj" :link="slotProps.obj.link" class="entry"></scene-entry>-->
      <!--这里既可以在子组件循环，也可以在父组件循环  :link="slotProps.obj.link"-->
      <!--</template>-->
      <!--可收集物品-->

      <!--不可收集物品-->
    </scene-one>
    <scene-two :scene="nowscene" v-if="level===2"></scene-two>
    <scene-three :scene="nowscene" v-if="level===3" ref="sceneThree"></scene-three>

    <!--背包栏-->
<div class="bagbg" v-show="havebag" @click="movebag" 
:style="{background: 'url('+ bagimg +')'}">
    <div class="mybag" :style="{left:bagleft+'px'}">
      <mybag-obj v-for="item in items" :item="item" :key="item.name" 
      :activeItem="activeItem"></mybag-obj>
    </div>
    <img v-show="showleft" class="bagleft" v-if="level===1"
    src="/static/1_0/bagleft.png" style="left:4px;">
    <img v-show="showright" class="bagright" v-if="level===1"
    src="/static/1_0/bagright.png" style="right:4px">
    <img v-show="showleft" class="bagleft" v-if="level===2"
    src="/static/2_0/bagleft.png" style="left:4px">
    <img v-show="showright" class="bagright" v-if="level===2"
    src="/static/2_0/bagright.png" style="right:4px">
    <img v-show="showleft" class="bagleft" v-if="level===3"
    src="/static/3_0/bagleft.png" style="left:1px;width:60px;height:54px;">
    <img v-show="showright" class="bagright" v-if="level===3"
    src="/static/3_0/bagright.png" style="right:1px;width:60px;height:54px;">
  </div>
  </div>
</template>

<script>
import MybagObj from "./MybagObj.vue";
import SceneOne from "./SceneOne.vue";
import SceneTwo from "./SceneTwo.vue";
import SceneThree from "./SceneThree.vue";



export default {
  name: "Level",
  components: {
    SceneOne,
    SceneTwo,
    SceneThree,
    MybagObj
  },
  props: ["startlevel"],
  data() {
    return {
      level:this.startlevel,
      havebag: null,
      bagimg:null,
      makepaopaot:false,
      items: [
       /* {
          name: "manyfish",
          bagimg: "/static/prop/feature/manyfish.png",
          text:"好多好多好多鱼",
          partner: ""
        },*/
       
       
      ],
      activeItem: null,
      bagleft:0,
      showright:false,
      showleft:false,
      getmiqifly:false,
      scenes: {},
      nowscene: {}
    };
  },

  methods: {
    setActiveItem(itemname) {
      this.activeItem = itemname;
    },
    changescene(scene) {
      this.nowscene = this.scenes[scene];
    },
    /*change(e) {
      if (e.target.className == "up") {
        this.nowscene = this.scenes[this.nowscene.up];
      } else if (e.target.className == "down") {
        this.nowscene = this.scenes[this.nowscene.down];
      } else if (e.target.className == "left") {
        this.nowscene = this.scenes[this.nowscene.left];
      } else if (e.target.className == "right") {
        this.nowscene = this.scenes[this.nowscene.right];
      } else if (e.target.className == "entry") {
        let link = e.target.getAttribute("link");
        this.nowscene = this.scenes[link];
      } else if (e.target.className == "collect") {
        let item = {};
        item.name = e.target.getAttribute("name");
        item.partner = e.target.getAttribute("partner");
        item.bagimg = e.target.getAttribute("bagimg");
        this.items.push(item);
      } else if (e.target.className == "cantget") {
        let partner = e.target.getAttribute("partner");
        if(partner){
        if (this.activeItem === partner) {
          this.removeItem(partner);
        }
      }
      }
    },*/
    addItem(item){
      this.items.push(item);
      if(this.items.length>6&&this.bagleft==0){
        this.showright=true;
      }
      if(this.items.length>12&&this.bagleft==-1875){
        this.showright=true;
      }
      if(this.items.length>18&&this.bagleft==-1875-1875){
        this.showright=true;
      }
    },
    removeItem(name) {
      let objArr = this.items.filter(function(item) {
        return name === item.name;
      });
      let index = this.items.indexOf(objArr[0]);
      this.items.splice(index, 1);
      this.activeItem = null;
      if(this.items.length<=6){
        this.showright=false;
        this.bagleft=0;
        this.showleft=false;
      }
      if(this.items.length<=12&&this.bagleft==-1875-1875){
        this.showright=false;
        this.bagleft+=1875;
      }
      if(this.items.length<=18&&this.bagleft==-1875-1875-1875){
        this.showright=false;
        this.bagleft+=1875;
      }
    },
    movebag(e){
      if (e.target.className == "bagleft") {
        this.bagleft+=1875;
        this.showright=true;
      } else if (e.target.className == "bagright") {
        this.bagleft-=1875;
        this.showright=false;
        if(this.items.length>12&&this.bagleft==-1875){
        this.showright=true;
      }
      if(this.items.length>18&&this.bagleft==-1875-1875){
        this.showright=true;
      }
      }
      if(this.bagleft==0){this.showleft=false;}
      else this.showleft=true;
    },
    level1done(){
      this.$parent.showturn=true;
      setTimeout(() => {
        this.$parent.level=2;
          //this.getData();
          document.getElementById("museum").play();
        document.getElementById("level1").currentTime = 0;
        document.getElementById("level1").pause();
      }, 1500);
      
    },
    level2done(){
      this.$parent.showturn=true;
      setTimeout(() => {
        this.$parent.level=3;
          //this.getData();
          document.getElementById("museum").play();
        document.getElementById("level2").currentTime = 0;
        document.getElementById("level2").pause();
      }, 1500);
      
    },
    getData() {
      if(this.level===1){
      this.axios.get("/R.json").then(res => {
        this.scenes=res.data.scenes;
        this.nowscene = this.scenes["start11"];
        this.bagimg=res.data.bagimg;
        this.havebag=res.data.havebag;
      });
      }else if(this.level===2){
        this.axios.get("/R2.json").then(res => {
        this.scenes=res.data.scenes;
        this.nowscene = this.scenes["end11"];
        this.bagimg=res.data.bagimg;
        this.havebag=res.data.havebag;
      });
      }else if(this.level===3){
        this.axios.get("/R3.json").then(res => {
        this.scenes=res.data.scenes;
        this.nowscene = this.scenes["end21"];
        this.bagimg=res.data.bagimg;
        this.havebag=res.data.havebag;

        let item = {};
        item.name = "memorandum";
        item.partner = "";
        item.bagimg = "/static/prop/feature3/memorandum.png";
        item.text="我的备忘本";
        item.show=false;
        this.addItem(item);   
      });
      }
    }
  },
  created() {
    this.getData();
  },
  mounted() {
    
  }
};
</script>

