      <p v-if="type==1" class="sp1">
        （前传 母亲视角）
        <br>
        <br>一
        <br>意外来得毫无预兆，正如这场盛夏的暴雨，噩耗挟着雷电劈裂天空，她一时耳鸣，竟听不见轰然炸响的雷。
        <br>她握着话筒站在客厅，浑然忘却通话何时结束，单调的忙音平静而固执地试图拉扯她的神经。
        <br>“轰隆隆——！！”
        <br>又是一声炸雷，这次她听清楚了，因为儿子乐乐在他的房间哭了起来，哭着叫妈妈。
        <br>她扔下话筒小跑进儿子房间，把吓哭的孩子紧紧拥进怀里，轻拍他的背哄他。她恍然觉得全身冻住的血液重新奔涌起来，把积攒起来的温暖毫无保留地给予怀里的宝贝 。
        <br>乐乐的小脸埋在她怀里哭着，她干脆蹬了鞋，抱着孩子一起裹进被窝里。“别怕，啊，妈妈在呢。”
        <br>乐乐在她胸前蹭着眼泪，小手紧抓着她的衣袖，抽噎着说：“妈妈，雷声好恐怖——”
        <br>话音未尽又一声惊雷砸落。乐乐身体一抖，打起了哭嗝。
        <br>她搂着孩子，支起身从床头抽了纸巾给他擦脸，另一只手一下一下地拍着他的背给他顺气，力道温柔而稳定。
        <br>她轻声道：“不怕，那是闪电孩子贪玩跑到大地上了，雷公公在天上找不到他，在叫他回家呢。”
        <br>乐乐闻着妈妈身上的味道，稍微安定了些：“那他叫那么大声干嘛，好吓人。”他委委屈屈地嘟起嘴。
        <br>她弯起嘴角，用轻快的语气说：“因为闪电孩子跑到了好~远好~远的地方啊，雷公公担心他听不见。”
        <br>她顿了一下，再开口，嗓音揉进了一把沙子：“他只是害怕，怕他找不到回家的路。”
        <br>“怕他忘了回家。”
        <br>
        <br>二
        <br>雨季过了，丈夫所有的身后事也都办好了。
        <br>她把丈夫的骨灰盒放在公共陵园里，细心擦净最后一点灰尘，摆上他们都喜欢的百合花。
        <br>她婉言谢绝了她和丈夫的同事和朋友们来家里探望的好意，也请他们保密，不要告诉她才上小学一年级的儿子。
        <br>今天是丈夫的生日。他平时忙，总是出差，这次好不容易轮到一次休假，一家人本计划着在一起好好庆祝的。可是一切约定都被那场雨带走了。
        <br>她久久立着，直到手机闹铃在她包里震动起来。
        <br>要去接乐乐放学了。
        <br>像往常一样，乐乐一路叽叽喳喳地跟她说着学校里的事，她努力打起精神回应着。乐乐似乎感觉到妈妈的异样，渐渐地收了话头。安静了十秒，她恍然惊觉，回头询问地看向乐乐。
        <br>乐乐低着头，只给她一个乌黑的发旋。她拉着他的手蹲下身，问他怎么了。
        <br>“爸爸是不是又回不了家了？”他小小声地问。
        <br>她心中一酸，努力把涌上喉头的悲伤咽下去，清清嗓子开口：“爸爸有紧急任务，飞到很远的地方了。他刚给我打了电话，跟我们说对不起。”
        <br>她把嘴唇咬得发白，不敢说“下次”。
        <br>乐乐不高兴了，赌气不说话。她只能刮刮他的脸逗他：“呀，小嘴儿撅得都能挂上油瓶了。”
        <br>乐乐“哼”了一声，别过了头。
        <br>她无奈地起身，带孩子回家。
        <br>
      </p>

      <p v-if="type==2" class="sp2">
        （第二关前传 初中儿子方时乐视角）
        <br>
        <br>三
        <br>初一学到《爸爸的花儿落了》这篇课文的时候，方时乐早就知道了爸爸不在了的事实。但知道不等于接受。他用一种审视般的目光睨着课文里的“我”，比较着自己的和主人公的爸爸。
        <br>他的爸爸没有因为他不听话而打过他，更没有在生气之后还给自己送东西——妈妈做过。爸爸没有抱着他举过肩头——至少他印象中没有，也没有参加他的毕业典礼，是妈妈一直在他身后，看着他迈过一道又一道新的门槛。
        <br>实际上，他的记忆里已经没有多少爸爸的身影了。从小爸爸就经常出差，回来时为了补偿会给他带很多零食，一家人会出去玩或者吃饭作为庆祝。但后来，爸爸忙到连零食和生日礼物都是妈妈捎回来给他。再后来，他就知道从六岁起那些礼物都是妈妈买的，爸爸在那次出差后就从他的人生里缺席了。
        <br>方时乐想，凭什么呢？凭什么是他失去了父爱？凭什么爸爸总要把背影扔给他和妈妈？同学在旁边高兴地说着自己的爸爸给他做了什么什么的时候，他永远都不能插上话。他别扭又赌气地想走开，耳朵却忍不住伸长，从刺耳的只言片语中摸索父亲的形象。
        <br>妈妈知道他很介意父亲的缺失，于是很珍惜大大小小的活动和节日里母子俩相处的机会。即使是某个平凡无奇的日期，她也会和儿子做些游戏，赋予它特殊的意义，力求填补上儿子心里的空缺。方时乐也自然而然地，把对爸爸的那份期望转交给妈妈，希望妈妈能多陪陪他。
        <br>更何况，今天是他的生日。
        <br>但是打开家门时，他只看到了空荡荡的房间，妈妈不在。他从门口的小纸条上得知妈妈今晚临时加班了，嘴一撇，心里泛上委屈来。
        <br>坏妈妈，明明说好今天一起过的。
        <br>骗人是小狗！
        <br>他赌气地钻进房间，打算饿死也不吃饭。
        <br>两个小时之后他向自己的胃投降了。妈妈还没回来，他真的只能自己给自己做饭吃。他拖着步子伸手去拉冰箱门，却发现锁住了。
        <br>要密码？方时乐愣了一下，撅得老高的嘴终于放下来，心中的阴霾扫去大半。
        <br>原来妈妈不是毫无准备啊！
        <br>那么——这次的游戏又是什么呢？
        <br>
      </p>
      <p v-if="type==3" class="sp3">
        （第三关前传 母亲视角 放假回家大学生方时乐）
        <br>
        <br>四
        <br>我以为，自己只是记性变差了，其实也不足为道，平时多上点心就是了。
        <br>虽然离退休岁数还远着，上年纪了，脑子总有点不灵光的时候。有次我记着家里没盐了，下班之后去买了一包，回来才发现盐罐是满的。嘿，原来前天就买过了。还有一次睡过了头，发现自己过了死线还没交工作报告，吓得我从床上弹起来就去开电脑，结果管理员小哥告诉我我昨晚就交了。差点闹了个大乌龙，头发都要吓白几根。
        <br>我以为自己只是有些迷糊而已。
        <br>直到那次和乐乐打电话——啊，乐乐考上了XX大学，远着呢，有时周末他会给我打电话，跟我说说学校的事之类的。这孩子，多大人了，我问他有没有喜欢的女孩子时还害羞呢，说自己忙。哎，不管怎样，有事干就好，别游手好闲的。
        <br>——我刚想写什么来着？啊，是了。那次和乐乐打电话聊到他小时候的事，说着说着，话筒那边乐乐突然出声：“妈妈，这个你刚刚说过了啊。”
        <br>“啊？我说过了？不会吧，我刚刚说的是带你去水族馆那回啊。”
        <br>“那是上上件事啦，你刚刚说的就是看我文艺汇演的事。你现在在说第二遍。”
        <br>我的心陡然一坠，慌乱的气息被话筒上的小孔吞噬进线路和电波交错的黑洞里。
        <br>乐乐没有察觉什么，他被电波重构后的声音有些挫败地说着：“妈妈，我知道很好笑，但唱歌破音这种黑历史就不用再提第二次了。”他的尾音还是像小时候那样软软的，无意识地撒娇。
        <br>我“嗯”地应声了，又逗了逗他。电话结束后，上扬的嘴角和话筒一起落下。
        <br>我马上预约了医院的挂号。
        <br>“阿尔兹海默症。”戴着白口罩的医生说，“你是早老性痴呆。”
        <br>也就是说，我的记性会越来越差，我会重复做做过的事，会在工作时发呆，会忘记回家的路。到最后，会忘掉站在我面前的这个挺拔帅气的年轻人，是我的儿子，我的乐乐。
        <br>“妈妈，生日快乐！”他提起蛋糕盒子，在我面前求表扬似的轻轻晃了晃。
        <br>哦，今天原来是我的生日。
        <br>我没有先接过蛋糕，而是就着这个姿势向前抱住乐乐，他乐呵呵地微俯下肩，也抱住了我。哎，一眨眼，乐乐的个子就窜得比我还高了。
        <br>像他爸爸那么高。
        <br>我低头接过蛋糕盒子，不想让他看到红起来的眼眶，嘴里絮絮叨叨找话：“你今天不是去和以前的同学看电影吗？不一起吃饭？”我在做的回忆录差点就来不及收好了，可把我吓得。
        <br>话是这么说，但我还是忍不住弯起嘴角。
        <br>“反正都放假了，下次还可以再约。但今天是妈妈生日啊！”他手脚麻利地插好蜡烛，点亮一小朵一小朵火焰，眼睛里闪着光，“我们以前就约好了，生日都要一起过！”他把蛋糕推到我面前，催促我许愿。
        <br>烛光被泪水模糊成大块大块的朦胧光晕，我闭上眼睛，郑重地许下一个愿望。
        <br>上天啊，求你慢点收走我的记忆，让我再陪乐乐久一点吧。
        <br>晚一点说出那句“你是谁”吧。
        <br>
      </p>
