儿撅得都能挂上油瓶了。”\n      "),_c('br'),_vm._v("乐乐“哼”了一声，别过了头。\n      "),_c('br'),_vm._v("她无奈地起身，带孩子回家。\n      "),_c('br')]):_vm._e(),(_vm.type==2)?_c('p',{staticClass:"sp2"},[_vm._v("\n      （第二关前传 初中儿子方时乐视角）\n      "),_c('br'),_c('br'),_vm._v("三\n      "),_c('br'),_vm._v("初一学到《爸爸的花儿落了》这篇课文的时候，方时乐早就知道了爸爸不在了的事实。但知道不等于接受。他用一种审视般的目光睨着课文里的“我”，比较着自己的和主人公的爸爸。\n      "),_c('br'),_vm._v("他的爸爸没有因为他不听话而打过他，更没有在生气之后还给自己送东西——妈妈做过。爸爸没有抱着他举过肩头——至少他印象中没有，也没有参加他的毕业典礼，是妈妈一直在他身后，看着他迈过一道又一道新的门槛。\n      "),_c('br'),_vm._v("实际上，他的记忆里已经没有多少爸爸的身影了。从小爸爸就经常出差，回来时为了补偿会给他带很多零食，一家人会出去玩或者吃饭作为庆祝。但后来，爸爸忙到连零食和生日礼物都是妈妈捎回来给他。再后来，他就知道从六岁起那些礼物都是妈妈买的，爸爸在那次出差后就从他的人生里缺席了。\n      "),_c('br'),_vm._v("方时乐想，凭什么呢？凭什么是他失去了父爱？凭什么爸爸总要把背影扔给他和妈妈？同学在旁边高兴地说着自己的爸爸给他做了什么什么的时候，他永远都不能插上话。他别扭又赌气地想走开，耳朵却忍不住伸长，从刺耳的只言片语中摸索父亲的形象。\n      "),_c('br'),_vm._v("妈妈知道他很介意父亲的缺失，于是很珍惜大大小小的活动和节日里母子俩相处的机会。即使是某个平凡无奇的日期，她也会和儿子做些游戏，赋予它特殊的意义，力求填补上儿子心里的空缺。方时乐也自然而然地，把对爸爸的那份期望转交给妈妈，希望妈妈能多陪陪他。\n      "),_c('br'),_vm._v("更何况，今天是他的生日。\n      "),_c('br'),_vm._v("但是打开家门时，他只看到了空荡荡的房间，妈妈不在。他从门口的小纸条上得知妈妈今晚临时加班了，嘴一撇，心里泛上委屈来。\n      "),_c('br'),_vm._v("坏妈妈，明明说好今天一起过的。\n      "),_c('br'),_vm._v("骗人是小狗！\n      "),_c('br'),_vm._v("他赌气地钻进房间，打算饿死也不吃饭。\n      "),_c('br'),_vm._v("两个小时之后他向自己的胃投降了。妈妈还没回来，他真的只能自己给自己做饭吃。他拖着步子伸手去拉冰箱门，却发现锁住了。\n      "),_c('br'),_vm._v("要密码？方时乐愣了一下，撅得老高的嘴终于放下来，心中的阴霾扫去大半。\n      "),_c('br'),_vm._v("原来妈妈不是毫无准备啊！\n      "),_c('br'),_vm._v("那么——这次的游戏又是什么呢？\n      "),_c('br')]):_vm._e(),(_vm.type==3)?_c('p',{staticClass:"sp3"},[_vm._v("\n      （第三关前传 母亲视角 放假回家大学生方时乐）\n      "),_c('br'),_c('br'),_vm._v("四\n      "),_c('br'),_vm._v("我以为，自己只是记性变差了，其实也不足为道，平时多上点心就是了。\n      "),_c('br'),_vm._v("虽然离退休岁数还远着，上年纪了，脑子总有点不灵光的时候。有次我记着家里没盐了，下班之后去买了一包，回来才发现盐罐是满的。嘿，原来前天就买过了。还有一次睡过了头，发现自己过了死线还没交工作报告，吓得我从床上弹起来就去开电脑，结果管理员小哥告诉我我昨晚就交了。差点闹了个大乌龙，头发都要吓白几根。\n      "),_c('br'),_vm._v("我以为自己只是有些迷糊而已。\n      "),_c('br'),_vm._v("直到那次和乐乐打电话——啊，乐乐考上了XX大学，远着呢，有时周末他会给我打电话，跟我说说学校的事之类的。这孩子，多大人了，我问他有没有喜欢的女孩子时还害羞呢，说自己忙。哎，不管怎样，有事干就好，别游手好闲的。\n      "),_c('br'),_vm._v("——我刚想写什么来着？啊，