<template>
  <div class="ctn" @click="eventslisten">
    <img v-show="state===1" src="/static/1_1/notebookhw.png" 
    style="width:714px;height:768px;top:20px;left:600px">
    <img v-show="state===0" src="/static/1_1/notebookfish.png" 
    style="width:714px;height:750px;top:38px;left:600px">
    <img
      class="read" v-show="state===0"
      src="/static/1_0/read.png"
      style=" position: absolute; top: 640px; left: 1230px;
  width:42px;height:42px"
    >
    <img
      class="read" v-show="state===1"
      src="/static/1_0/read.png"
      style=" position: absolute; top: 300px; left: 1230px;
  width:42px;height:42px"
    >
    <img
        class="myleft"
        v-show="state===1"
        src="/static/1_1/nbleft.png"
        width="36px"
        height="66px"
        style="top:270px;left:540px"
      >
      <img
        class="myright"
        v-show="state===0"
        src="/static/1_1/nbright.png"
        width="36px"
        height="66px"
        style="top:390px;left:1350px"
      >
    <img src="/static/museum/down2.png" class="mydown">
    <div class="moreinfo" v-show="reading&&state===1">
      <p>·老师布置我们预习P87，明天要提问</p>
      <p>·今天的任务是哄妈妈开心！</p>
      <p>·把零食像小卖部那样摆成彩虹的颜色，</p>
      <p>&emsp;就能生出好多好多零食了！</p>
      <p>·我还想要一只小飞鼠做店员！</p>
    </div>
    <div class="moreinfo" v-show="reading&&state===0">
      <p>今天和爸爸妈妈一起去广场小摊</p>
      <p>上捞鱼玩，捞到了好多鱼！</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "NoteBook",
  components: {},
  props: [],
  data: function() {
    return {
      reading: false,
      state:1
    };
  },
  computed: {},
  methods: {
    eventslisten(e) {
      if (e.target.className == "read") {
        this.reading = true;
        document.getElementById("btn").play();
      } else if (e.target.className == "mydown") {
        this.$off();
        this.$parent.feaNoteBook = false;
        //document.getElementById("btn").play();
      } else if (e.target.className == "moreinfo") {
        this.reading = false;
      }else if (e.target.className == "myright") {
        this.state = 1;
        document.getElementById("btn").play();
      } else if (e.target.className == "myleft") {
        this.state = 0;
        document.getElementById("btn").play();
      }
    }
  },
  created(){

  }
};
</script>

<style scoped lang="scss">
.ctn {
  background-color: rgba(0, 0, 0, 0.7);
}
img{
    position: absolute;
    z-index: 4;
}
.myleft,.myright,.read{
    cursor: pointer;
}
.mydown {
  position: absolute;
  width:70px;
  height:40px;
  left: 49%;
  top: 70%;
  z-index: 8;
  cursor: pointer;
}
.moreinfo {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 8;
  background-color: rgba(0, 0, 0, 0.8);
  padding-top: 300px;
  padding-left: 600px;
  cursor: pointer;
}

p {
  color: white;
  margin-top: 40px;
  font-family: "myfont";
  font-weight: normal;
  font-size: 40px;
}
</style>