<template>
  <div class="numdiv" @click="add">
    <div class="numdiv1" style="top:0px;left:5px;">{{num1}}</div>
    <div class="numdiv2" style="top:0px;left:65px;">{{num2}}</div>
    <div class="numdiv3" style="top:0px;left:125px;">{{num3}}</div>
    <div class="numdiv4" style="top:0px;left:185px;">{{num4}}</div>
  </div>
</template>

<script>
export default {
  name: "PPassword",
  components: {},
  props: ["num"],
  data: function() {
    return {
      num1: 0,
      num2: 0,
      num3: 0,
      num4: 0
    };
  },
  computed: {},
  methods: {
    add: function(e) {
      if (e.target.className == "numdiv1") {
        this.num1 = (this.num1 + 1) % 10;
        document.getElementById("password").play();
      } else if (e.target.className == "numdiv2") {
        this.num2 = (this.num2 + 1) % 10;
        document.getElementById("password").play();
      } else if (e.target.className == "numdiv3") {
        this.num3 = (this.num3 + 1) % 10;
        document.getElementById("password").play();
      } else if (e.target.className == "numdiv4") {
        this.num4 = (this.num4 + 1) % 10;
        document.getElementById("password").play();
      }
      if (this.num === 1) {
        if (
          this.num1 == 1 &&
          this.num2 == 9 &&
          this.num3 == 9 &&
          this.num4 == 5
        ) {
          this.$parent.cantGetObjs[7].from = "apenguinbox";
          this.$parent.canGetObjs[13].from = "apenguinbox";
        } else {
          this.$parent.cantGetObjs[7].from = "";
          this.$parent.canGetObjs[13].from = "";
        }
      }
      if (this.num === 2) {
        if (
          this.num1 == 0 &&
          this.num2 == 5 &&
          this.num3 == 0 &&
          this.num4 == 8
        ) {
          this.$parent.cantGetObjs[6].from = "apenguinbox";
          this.$parent.canGetObjs[12].from = "apenguinbox";
        } else {
          this.$parent.cantGetObjs[6].from = "";
          this.$parent.canGetObjs[12].from = "";
        }
      }
      if (this.num === 3) {
        if (
          this.num1 == 0 &&
          this.num2 == 3 &&
          this.num3 == 2 &&
          this.num4 == 1
        ) {
          this.$parent.cantGetObjs[8].from = "apenguinbox";
          if (!this.$parent.canGetObjs[14].isget)
            this.$parent.cantGetObjs[9].from = "apenguinbox";
        } else {
          this.$parent.cantGetObjs[8].from = "";
          this.$parent.cantGetObjs[9].from = ""; //如果未拿要隐藏
        }
      }
    }
  }
};
</script>

<style scoped lang="scss">
.numdiv div {
  position: absolute;
  text-align: center;
  font-size: 80px;
  cursor: pointer;
}
</style>
