<template>
  <div class="numdiv" @click="add">
    <div class="numdiv1" style="top:560px;left:564px;">{{num1}}</div>
    <div class="numdiv2" style="top:560px;left:892px;">{{num2}}</div>
    <div class="numdiv3" style="top:560px;left:1221px;">{{num3}}</div>
  </div>
</template>

<script>
export default {
  name: "TPassword",
  components: {},
  props: [],
  data: function() {
    return {
      num1: 0,
      num2: 0,
      num3: 0
    };
  },
  computed: {},
  methods: {
    add: function(e) {
      if (e.target.className == "numdiv1") {
        this.num1 = (this.num1 + 1) % 10;
        document.getElementById("password").play();
      } else if (e.target.className == "numdiv2") {
        this.num2 = (this.num2 + 1) % 10;
        document.getElementById("password").play();
      } else if (e.target.className == "numdiv3") {
        this.num3 = (this.num3 + 1) % 10;
        document.getElementById("password").play();
      }
      if (this.num1 == 2 && this.num2 == 9 && this.num3 == 3) {
        this.$parent.cantGetObjs[16].from = "atoybox";
        this.$parent.canGetObjs[20].from = "atoybox";
      }else{this.$parent.cantGetObjs[16].from = "";
      this.$parent.canGetObjs[20].from = "";}
    }
  }
};
</script>

<style scoped lang="scss">
.numdiv div {
  position: absolute;
  text-align: center;
  font-size: 160px;
  cursor: pointer;
}
</style>
