<template>
  <div @click="change">
    <audio id="lbird" src="/static/第二关/lbird.mp3" hidden="true"></audio>
    <audio id="openlock" src="/static/第二关/打开冰箱锁.mp3" hidden="true"></audio>
    <audio id="openink" src="/static/第二关/扭开墨水瓶.mp3" hidden="true"></audio>
    <audio id="pencilnote" src="/static/第二关/便条本.mp3" hidden="true"></audio>
    <audio id="drawer" src="/static/第二关/锁住的抽屉.mp3" hidden="true"></audio>
    <audio id="relock" src="/static/第二关/直接点击冰箱锁.mp3" hidden="true"></audio>
    

    <div class="container">
      <!--博物馆入口-->
      <img v-if="scene.name=='start21'" src="static/museum/entry2.png" class="roomentry">

      <!--可近景物品-->
      <inner-obj
        v-for="obj in objs"
        :key="obj.name"
        :obj="obj"
        :link="obj.link"
        :name="obj.name"
        class="entry"
      ></inner-obj>

      <!--可被作用物品，不可收集-->
      <inner-obj
        v-for="obj in objs3"
        :key="obj.name"
        :obj="obj"
        :partner="obj.partner"
        :name="obj.name"
        class="cantget"
      ></inner-obj>

      <!--可收集物品-->
      <div v-for="obj in objs2" :key="obj.name">
        <inner-obj v-if="!obj.isget" :obj="obj" @click.native="getobj2($event,obj)"></inner-obj>
      </div>

      <!--可触发对话框对象-->
      <div v-for="obj in oneSceneIns" :key="obj.name">
        <inner-obj :obj="obj" @click.native="hideDialog(obj)"></inner-obj>
      </div>

      <!--其他-->
      <img class="whitenote" src="/static/2_1/inknotewhite.png" v-show="showwhitenote">
      <img class="purple" src="/static/2_1/purple.png" v-show="showpurple">

      <div class="fealock" v-if="fealock">
        <img src="/static/2_2/alock.png" class="lockimg">
        <div class="password">
          <div class="numdiv1" style="top:526px;left:787px;">{{num1}}</div>
          <div class="numdiv2" style="top:535px;left:880px;">{{num2}}</div>
          <div class="numdiv3" style="top:535px;left:980px;">{{num3}}</div>
          <div class="numdiv4" style="top:523px;left:1070px;">{{num4}}</div>
          <!-- <audio id="password" src="/static/第一关/电视柜密码.mp3" hidden="true"></audio>-->
        </div>
        <img src="/static/museum/down2.png" class="mydown">
      </div>

      <img src="/static/2_1/mouse.png" class="mouse" v-show="showmouse&&scene.name=='livingroom'">
      <img
        src="/static/2_1/mouseback.png"
        class="mouseback"
        v-show="showback&&scene.name=='livingroom'"
      >
      <img src="/static/2_1/read.png" class="mouseread" v-show="showread&&scene.name=='livingroom'">
      <img src="/static/2_1/note4.png" class="note4" v-show="shownote4&&scene.name=='livingroom'">

      <div class="mousetext" v-if="showmousedlg">我是老鼠商人，召唤我的人类啊，用海洋的馈赠来换取失落的信息吧。</div>

      <!--场景切换箭头-->
     <!-- <div class="up" v-if="scene.up"></div>
      <div class="down" v-if="scene.down"></div>
      <div class="left" v-if="scene.left"></div>
      <div class="right" v-if="scene.right"></div>-->
      <img src="/static/museum/down2.png" class="down" v-if="scene.down&&scene.name!='end12'">
      <img src="/static/museum/right2.png" class="right" v-if="scene.right">
      <img src="/static/museum/right2.png" class="left" v-if="scene.left">
    </div>

    <!--博物馆下箭头-->
    <img src="/static/museum/down2.png" class="museumdown" v-if="scene.name=='end12'">
    <!--黑屏-->
    <div class="blackscreen" v-show="showblack"></div>
    <!--对话框-->
    <div class="dialog" v-if="!hideDlg">
      <my-dialog :texts="dlgTexts"></my-dialog>
      <img v-if="showmom" src="/static/2_0/mom.png" width="330px" height="540px" style="left:0">
      <img
        v-else-if="showson2"
        src="/static/museum/son1.png"
        width="360px"
        height="660px"
        style="left:0px"
      >
      <img
        v-else-if="showson"
        src="/static/2_0/son.png"
        width="258px"
        height="414px"
        style="left:40px"
      >
      
    </div>

    <!--特写图，有阅读按钮-->
    <calendar class="fea" v-if="feaCal" :getnote4="getnote4">
      <inner-obj
        v-if="!canGetObjs[9].isget&&canGetObjs[9].from=='cal'"
        :obj="canGetObjs[9]"
        class="obj9"
      ></inner-obj>
    </calendar>

    <img src="/static/museum/miqi.png" class="miqimuseum" v-if="scene.name=='end12'">

    <img src="/static/enlarge/middle.png" class="endmiddle" v-if="showend2">

    <story v-if="showstory" :type="storytype"></story>

  </div>

</template>

<script>
import InnerObj from "./InnerObj.vue";
import MyDialog from "./MyDialog.vue";
import Calendar from "./Calendar.vue";
import Story from "./Story.vue";
import bus from "./bus.js";


//import axios from "axios";

export default {
  name: "SceneTwo",
  components: { InnerObj, MyDialog, Calendar,Story },
  props: ["scene"],
  data: function() {
    return {
      //scene: this.scene,
      hideDlg: true,
      dlgTexts: [],
      showmom: false,
      showson: true,
      showson2: true,
      feaCal: false,
      showpurple: false,
      showwhitenote:false,
      hadopen: false,
      fealock: false,
      num1: 0,
      num2: 0,
      num3: 0,
      num4: 0,
      hadunlock: false,
      showmouse: false,
      showread: false,
      showback: false,
      showmousedlg: false,
      shownote4: false,
      showend2:false,
      getnote4: false,
      getnote4f: true,
      firstdlg: true,

      lrkcnotechange: false,
      makelaver: false,

      showblack: false,

      showstory:false,
      storytype:1,

      linkobjs: [],

      canGetObjs: [],

      cantGetObjs: [],

      instru: []
    };
  },
  computed: {
    objs: function() {
      let that = this;
      return this.linkobjs.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    objs2: function() {
      let that = this;
      return this.canGetObjs.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    oneSceneIns: function() {
      let that = this;
      return this.instru.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    objs3: function() {
      let that = this;
      return this.cantGetObjs.filter(function(item) {
        return item.from === that.scene.name;
      });
    }
  },
  methods: {
    change(e) {
      if (e.target.className == "up") {
        this.$parent.changescene(this.scene.up);
      } else if (e.target.className == "down"||e.target.className == "museumdown") {
        this.$parent.changescene(this.scene.down);
        //document.getElementById("btn").play();
      } else if (e.target.className == "left") {
        this.$parent.changescene(this.scene.left);
        //document.getElementById("btn").play();
      } else if (e.target.className == "right") {
        this.$parent.changescene(this.scene.right);
        //document.getElementById("btn").play();
      } else if (e.target.className == "roomentry") {
        this.$parent.$parent.showturn=true;
        this.showson2=false;
        this.showson=true;
        let that=this;
        setTimeout(function(){
         document.getElementById("level2").play();
        document.getElementById("museum").currentTime = 0;
        document.getElementById("museum").pause();
        that.$parent.changescene("afterschool");
        that.$parent.havebag = false;
        that.hideDlg=false;
        that.dlgTexts = [
          "妈妈！我回来了！",
          "……妈妈？",
          "妈妈去哪了？",
          "乐乐发现门上有一张便条，上面写着：乐乐，妈妈今晚临时加班。我在冰箱里放了吃的，饿了就自己解决晚饭吧。",
          "坏妈妈，今天是我的生日啊，明明说好一起过的。",
          "骗人是小狗！",
          "哼，妈妈不回来，我就算饿死在房间也不会吃的！"
        ];
        },2000);
        
      } else if (e.target.className == "entry") {
        this.$parent.changescene(e.target.getAttribute("link"));
      } else if (e.target.className == "cantget") {
        let partner = e.target.getAttribute("partner");
        let name = e.target.getAttribute("name");
        if (partner) {
          if (this.$parent.activeItem === partner) {
            if (partner == "lrkcnote" && this.lrkcnotechange) {
              //如果已经被剪
              //this.cantGetObjs[11].from = "adesk"; 
              this.showwhitenote=true;//出现notewhite
              var whitenote = document.getElementsByClassName("whitenote")[0];
              whitenote.addEventListener("webkitAnimationEnd", this.whitenotefunc);
        
            } else if (partner == "lrkcnote" && !this.lrkcnotechange) {
              //如果未被剪
              this.$parent.activeItem = null;
              return;
            }
            this.$parent.removeItem(partner);

            if (name == "emptynote") {
              new Image().src="/static/2_1/writing.png";
              document.getElementById("pencilnote").play();
              this.canGetObjs[8].from = "adesk"; //出现笔迹
              this.cantGetObjs[2].from = ""; //移走点击区域
            } else if (name == "cabinet") {
              this.canGetObjs[2].from = "livingroom"; //开柜子
              this.canGetObjs[6].from = "livingroom";
              this.canGetObjs[7].from = "livingroom";
              this.hadopen = true;

              document.getElementById("open").play();
            } else if (name == "birdclock") {
              document.getElementById("lbird").play();
              this.cantGetObjs[3].from = "livingroom"; //小鸟
              this.canGetObjs[1].from = "livingroom"; //钥匙
              this.canGetObjs[10].from = "livingroom"; //便条
            } else if (partner == "ribbon") {
              this.cantGetObjs[7].from = "livingroom"; //挂上彩带
              document.getElementById("mouse").play();
              this.showmouse = true;
              this.showread = true; //老鼠商人出现
            }
          } else {
            this.$parent.activeItem = null;
          }
        }
        if (name === "cabinet") {
          if (this.hadopen) {
            document.getElementById("open").play();
            this.canGetObjs[2].from = "livingroom"; //开柜子
            this.canGetObjs[6].from = "livingroom";
            this.canGetObjs[7].from = "livingroom";
          } else {
            this.hideDlg = false;
            this.dlgTexts = ["柜子被锁住了。"];
            document.getElementById("drawer").play();
          }
        } else if (name === "calendar") {
          this.feaCal = true;
          if (this.getnote4 && this.getnote4f) {
            this.hideDlg = false;
            this.dlgTexts = ["刚刚的便条说的是日历吧，翻翻看。"];
            this.getnote4f = false;
          }
        } else if (name === "cover") {
          document.getElementById("openink").play();
          this.cantGetObjs[12].from = "adesk"; //出现openink
          this.cantGetObjs[10].from = "adesk"; //出现back
          this.cantGetObjs[9].from = ""; //盖子消失
          this.cantGetObjs[8].from = ""; //ink消失
        } else if (name === "lock") {
          this.fealock = true;
          document.getElementById("relock").play();
        } else if (name === "unlock") {
          this.cantGetObjs[16].from = ""; //锁消失
          this.hadunlock = true;
        } else if (name === "bandage") {
          if (this.hadunlock) this.cantGetObjs[14].from = ""; //绑带消失
        } else if (name === "openrefri") {
          document.getElementById("openr").play();
          this.cantGetObjs[17].from = "kitchen"; //开冰箱
          this.cantGetObjs[18].from = "kitchen";//蛋糕
        }  else if (name === "refri") {
          document.getElementById("closer").play();
          this.cantGetObjs[17].from = "";//关冰箱
          this.cantGetObjs[18].from = "";//蛋糕
        } else if (name === "cake") {
          this.$parent.changescene("livingroom");
          document.getElementById("momback").play();
          //this.showend2=true;
          this.showson=true;
          this.showson2=false;
          this.hideDlg=false;
          this.dlgTexts=["妈妈，你终于回来啦！","呀，乐乐，你现在才解开密码吗？",
          "哼，都怪妈妈 ，明知今天是我生日还不陪我！",
          "（苦笑）突然加班，妈妈也没办法啊……抱歉啦，让乐乐久等了。一起吃生日蛋糕吧？",
          "乐乐你看，这个布丁是用你喜欢的QQ糖做出来的哦。",
          "这是什么？鱼吗？","是啊，它们在仰望星空……"];
        } else if (name === "bookpiece") {
          this.showstory=true;
        }
      } else if (e.target.className == "numdiv1") {
        document.getElementById("password").play();
        this.num1 = (this.num1 + 1) % 10;
        this.checkpw();
      } else if (e.target.className == "numdiv2") {
        document.getElementById("password").play();
        this.num2 = (this.num2 + 1) % 10;
        this.checkpw();
      } else if (e.target.className == "numdiv3") {
        document.getElementById("password").play();
        this.num3 = (this.num3 + 1) % 10;
        this.checkpw();
      } else if (e.target.className == "numdiv4") {
        document.getElementById("password").play();
        this.num4 = (this.num4 + 1) % 10;
        this.checkpw();
      } else if (e.target.className == "mydown") {
        this.fealock = false;
        //document.getElementById("btn").play();
        if (!this.$parent.havebag) this.$parent.havebag = true;
      } else if (e.target.className == "mouseread") {
        if (
          (this.$parent.activeItem === "purple" ||
            this.$parent.activeItem === "laverbag") &&
          this.makelaver
        ) {
          this.$parent.removeItem(this.$parent.activeItem);
          this.showread = false;
          this.showmouse = false;
          this.showback = true;
          var mouseback = document.getElementsByClassName("mouseback")[0];
          mouseback.addEventListener("webkitAnimationEnd", this.backfunc);
        } else {
          this.showmousedlg = true;
          //document.getElementById("btn").play();
          this.$parent.activeItem = null;
        }
      } else if (e.target.className == "mousetext") {
        this.showmousedlg = false;
        if (this.firstdlg) {
          this.hideDlg = false;
          this.dlgTexts = [
            "（海洋的馈赠？是指食物吗？可是我把零食都吃完了啊，冰箱也锁了，上哪去找东西给你吃……）",
            "（要不先用什么骗一下它吧？对不住啦！等我打开冰箱就用吃的补偿你！）"
          ];
          this.firstdlg = false;
        }
      } else if (e.target.className == "note4") {
        this.shownote4 = false;
        this.getnote4 = true;
        let item = {};
        item.name = "note4";
        item.partner = "";
        item.bagimg = "/static/prop/feature2/note4.png";
        item.text = "'日乘两位数月走一位数（打一物品）'";
        this.$parent.addItem(item);
      } else if (e.target.className == "obj9") {
        this.getobj2(e, this.canGetObjs[9]);
      } else if (e.target.className == "sp1") {
        this.showstory=false;
      }
    },
    getobj2(event, obj) {
      event.stopPropagation();
      //event.cancelBubble = true;
      if (obj.name == "cabinetopen") {
        //event.stopPropagation();
        obj.from = "";
        document.getElementById("close").play();
        if (this.canGetObjs[6].isget === false) {
          this.canGetObjs[6].from = "";
        }
        if (this.canGetObjs[7].isget === false) {
          this.canGetObjs[7].from = "";
        }
        return;
      } else if (obj.name == "card") {
        this.hideDlg = false;
        this.dlgTexts = ["诶，密码出来了——！"];
      }

      obj.isget = true;
      let item = {};
      item.name = obj.name;
      item.partner = obj.partner;
      item.bagimg = obj.bagimg;
      item.text = obj.text;
      item.show = true;
      this.$parent.addItem(item);
    },
    hideDialog(obj) {
      this.hideDlg = false;
      this.dlgTexts = obj.text;

      if (obj.name == "mom") {
        this.showmom = true;
      } else {
        this.showmom = false;
      }
      if (obj.name == "emptynotesay") this.instru[1].from = "";
      //只能触发文本一次
      else if (obj.name == "birdclocksay") this.instru[2].from = "";
      else if (obj.name == "gouzisay" || obj.name == "gouzisay2") {
        this.instru[3].from = "";
        this.instru[4].from = "";
      }
    },
    purplefunc() {
      this.showwhitenote=false; //隐藏notewhite
      this.canGetObjs[11].from = "adesk"; //出现伪紫菜

      this.showpurple = false;
      var purple = document.getElementsByClassName("purple")[0];
      purple.removeEventListener("webkitAnimationEnd", this.purplefunc);
    },
    checkpw() {
      //document.getElementById("password").play();
      if (
        this.num1 == 3 &&
        this.num2 == 4 &&
        this.num3 == 2 &&
        this.num4 == 1
      ) {
        document.getElementById("openlock").play();
        this.cantGetObjs[16].from = "kitchen"; //开锁
        this.cantGetObjs[15].from = "";
      } else {
        this.cantGetObjs[16].from = "";
        this.cantGetObjs[15].from = "kitchen";
      }
    },
    backfunc() {
      this.showback = false;
      this.showmouse = true;
      this.shownote4 = true;
      var mouseback = document.getElementsByClassName("mouseback")[0];
      mouseback.removeEventListener("webkitAnimationEnd", this.backfunc);
      new Image().src="/static/2_1/card.png";
    },
    turnblack() {
      this.showblack = true;
      var blackscreen = document.getElementsByClassName("blackscreen")[0];
      blackscreen.addEventListener("webkitAnimationEnd", this.blackfunc);
    },
    blackfunc() {
      this.showson = false;
      this.hideDlg = false;
      this.dlgTexts = ["（两小时后）"];
      this.$parent.scenes["afterschool"].imgsrc = "/static/2_1/indark.png";
      var blackscreen = document.getElementsByClassName("blackscreen")[0];
      blackscreen.removeEventListener("webkitAnimationEnd", this.blackfunc);
    },
    whitenotefunc(){
      //黑渐变
              this.showpurple = true;
              var whitenote = document.getElementsByClassName("whitenote")[0];
              whitenote.removeEventListener("webkitAnimationEnd", this.whitenotefunc);
             
              
              var purple = document.getElementsByClassName("purple")[0];
              purple.addEventListener("webkitAnimationEnd", this.purplefunc);
    },
    hideblack() {
      document
        .getElementsByClassName("blackscreen")[0]
        .classList.add("hideblackscreen");
      document
        .getElementsByClassName("blackscreen")[0]
        .classList.remove("blackscreen");
      var blackscreen = document.getElementsByClassName("hideblackscreen")[0];
      blackscreen.addEventListener("webkitAnimationEnd", this.hideblackfunc);
    },
    hideblackfunc() {
      this.showson = true;
      this.showblack = false;
      this.hideDlg = false;
      this.dlgTexts = ["真的好饿，受不了了。去冰箱拿东西吃吧。"];
      var blackscreen = document.getElementsByClassName("hideblackscreen")[0];
      blackscreen.removeEventListener("webkitAnimationEnd", this.hideblackfunc);
    },
    getData() {
      this.axios.get("/R2.json").then(res => {
        this.linkobjs = res.data.linkobjs;
        this.canGetObjs = res.data.canGetObjs;
        this.cantGetObjs = res.data.cantGetObjs;
        this.instru = res.data.instru;
      });
    }
  },
  mounted() {
    bus.$on("updatestatus", name => {
      //这里最好用箭头函数，不然this指向有问题
      if (name === "lrkcnote") {
        this.lrkcnotechange = true;
      }
      if (name === "laver") {
        this.makelaver = true;
      }
    });

    //图片预加载
    new Image().src="/static/2_1/openink.png";
    new Image().src="/static/2_0/squareon.png";
    
    this.hideDlg=false;
    this.dlgTexts=["……结果爸爸一直都没有回来啊。"];
  },
  created() {
    this.getData();
  }
};
</script>

<style scoped>
.container {
  position: absolute;
  width: 100%;
  height: 75%;
}

.dialog {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  /*background-color: rgba(0, 0, 0, 0.5);*/
}
.dialog img {
  position: absolute;
  bottom: 0;
}
.fea {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 8;
}
scene-entry {
  cursor: pointer;
}
.whitenote{
  position: absolute;
  width: 120px;
  height: 348px;
  left: 1194px;
  animation: whitenoteanim 2s linear;
  -webkit-animation: whitenoteanim 2s linear;
  animation-fill-mode: forwards;
  -webkit-animation-fill-mode: forwards;
}
@keyframes whitenoteanim {
  from {
    top: 25px;
  }
  to {
    top: 90px;
  }
}
@-webkit-keyframes whitenoteanim {
  from {
    top: 25px;
  }
  to {
    top: 90px;
  }
}
.purple {
  position: absolute;
  width: 120px;
  height: 156px;
  left: 1194px;
  top: 90px;
  animation: purpleanim 2s linear;
  -webkit-animation: purpleanim 2s linear;
  animation-fill-mode: forwards;
  -webkit-animation-fill-mode: forwards;
}
@keyframes purpleanim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes purpleanim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fealock {
  position: absolute;
  top: 0;
  left: 0;
  width: 1920px;
  height: 1080px;
  z-index: 8;
  background-color: rgba(0, 0, 0, 0.4);
}
.lockimg {
  position: absolute;
  width: 702px;
  height: 1080px;
  margin-left: 600px;
}
.mydown {
  position: absolute;
  width:70px;
  height:40px;
  left: 49%;
  top: 68%;
  z-index: 8;
