<template>
  <div class="numdiv" @click="add">
    <div class="numdiv1" style="top:355px;left:631px;">{{num1}}</div>
    <div class="numdiv2" style="top:355px;left:787px;">{{num2}}</div>
    <div class="numdiv3" style="top:355px;left:943px;">{{num3}}</div>
  </div>
</template>

<script>
export default {
  name: "Password",
  components: {},
  props: [],
  data: function() {
    return {
      num1: 0,
      num2: 0,
      num3: 0
    };
  },
  computed: {},
  methods: {
    add: function(e) {
      if (e.target.className == "numdiv1") {
        this.num1 = (this.num1 + 1) % 10;
        document.getElementById("password").play();
      } else if (e.target.className == "numdiv2") {
        this.num2 = (this.num2 + 1) % 10;
        document.getElementById("password").play();
      } else if (e.target.className == "numdiv3") {
        this.num3 = (this.num3 + 1) % 10;
        document.getElementById("password").play();
      }
      if (this.num1 == 4 && this.num2 == 6 && this.num3 == 7) {
        this.$parent.linkobjs[7].from = "tvclose";
        this.$parent.linkobjs[2].link = "tvopen";
        new Image().src="/static/1_1/orangeQQ.png";
        new Image().src="/static/1_1/fishnet.png";
      }else{
        this.$parent.linkobjs[7].from = "";
      this.$parent.linkobjs[2].link = "tvclose";
      }
    }
  }
};
</script>

<style scoped lang="scss">
.numdiv div {
  position: absolute;
  width: 132px;
  height: 186px;
  text-align: center;
  font-size: 160px;
  cursor: pointer;
}
</style>
