<template>
  <img
    @mousedown="ButtonDown"
    :src="obj.imgsrc"
    :style="{ position: 'absolute', top: CurrentY+'px', left: CurrentX +'px',
  width:obj.w+'px',height:obj.h+'px',zIndex:zindex}"
  >
</template>

<script>
export default {
  name: "JigsawObj",
  components: {},
  props: ["obj"],
  data: function() {
    return {
      zindex: this.obj.zindex,
      X: 0,
      Y: 0,
      L: 0,
      T: 0,
      CurrentX: this.obj.x,
      CurrentY: this.obj.y,
    };
  },
  computed: {},
  methods: {
    moveStart(e) {
      let dv = e.target;

      this.X = e.clientX;
      this.Y = e.clientY;

      this.L = dv.offsetLeft;
      this.T = dv.offsetTop;
    },
    Move(e) {
      let nx = e.clientX;
      let ny = e.clientY;
      let nl = nx - (this.X - this.L);
      let nt = ny - (this.Y - this.T);

      this.CurrentX = nl;
      this.CurrentY = nt;
    },
    moveEnd(e) {
      if (
        this.CurrentX > this.obj.rx - 60 &&
        this.CurrentX < this.obj.rx + 60 &&
        this.CurrentY > this.obj.ry - 40 &&
        this.CurrentY < this.obj.ry + 40
      ) {
        this.CurrentX = this.obj.rx;
        this.CurrentY = this.obj.ry;
        this.$parent.jigsawobjs[this.obj.index].canmove=false;
      }
      if(this.CurrentY >820){
          let item = {};
          let obj=this.$parent.canGetObjs[this.obj.pindex];
      item.name = obj.name;
      item.partner = obj.partner;
      item.bagimg = obj.bagimg;
      item.text=obj.text;
      this.$parent.$parent.addItem(item);
      this.$parent.jigsawobjs[this.obj.index].from="";
      }
      this.zindex = 0;
      window.removeEventListener("mousemove", this.Move);
      window.removeEventListener("mouseup", this.moveEnd);
      this.$parent.jigsawobjs[this.obj.index].x=this.CurrentX;
      this.$parent.jigsawobjs[this.obj.index].y=this.CurrentY;
    },
    ButtonDown(e) {
      if (this.obj.canmove) {
        this.zindex = 1;
        this.moveStart(e);
        window.addEventListener("mousemove", this.Move);
        window.addEventListener("mouseup", this.moveEnd);
      }
    }
  }
};
</script>

<style scoped lang="scss">
img {
  cursor: pointer;
}
</style>