<template>
  <div class="modal" @click.stop="go">
    <div class="txt">
      <p>{{text || firstText}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "MyDialog",
  components: {},
  props: {
    texts: Array
  },
  data: function() {
    return {
      text: null,
      nextIndex:1,
      turnblack:false,
    };
  },
  computed: {
    it: function() {
      return this.makeIterator(this.texts);
    },
    firstText: function() {
      return this.texts[0];
    }
  },
  methods: {
    makeIterator(array) {
      let that=this;
      return {
        next: function() {
          return that.nextIndex < array.length
            ? { value: array[that.nextIndex++], done: false }
            : { done: true };
        }
      };
    },
    go() {
      let item = this.it.next();
      if (item.done) {
        this.text = null;
        this.$parent.hideDlg=true;
        if(this.turnblack){
          this.$parent.turnblack();//屏幕过渡
          this.turnblack=false;
        }
        if(this.firstText==="（两小时后）"){
          this.$parent.hideblack();
        }
        if(this.firstText==="真的好饿，受不了了。去冰箱拿东西吃吧。"){
          this.$parent.$parent.changescene("kitchen");
          this.$parent.fealock=true;
          this.$parent.hideDlg=false;
          this.$parent.dlgTexts=["哇，怎么有这么大一把锁！"];
        }
        if(this.firstText==="啊，这是妈妈给我过生日时拍的照片。"){
          this.$parent.showentry1=true;
        }
        if(this.firstText==="妈妈，你终于回来啦！"){
          this.$parent.$parent.havebag=false;
          this.$parent.showend2=true;
          setTimeout(() => {
            this.$parent.$parent.level2done();
          }, 6000);
         
        }
        if(this.firstText==="叫妈妈来看吧！"){
         this.$parent.$parent.changescene("livingroom");
         
         this.$parent.hideDlg=false;
         this.$parent.showmom=true;
         this.$parent.dlgTexts=["呀，这是什么……？",
         "妈妈你看！我把零食放在了一起，然后它们就会生出好多好多零食孩子，像小卖部那么多了！",
         "等爸爸回来，我们就能一起吃更多零食了！",
         "我还招了小飞鼠做店员呢！是不是很帅？","……妈妈？你怎么哭了呀？",
         "（哽咽）没事……妈妈只是太高兴了。乐乐真棒。","爸爸……爸爸一定会很喜欢的。",
         "（慌忙擦眼泪）啊，对了，乐乐是怎么想到零食生孩子的？",
         "（兴高采烈）我听说……"];
        }
        if(this.firstText==="呀，这是什么……？"){
          this.$parent.$parent.havebag=false;
          this.$parent.showend1=true;
          setTimeout(() => {
            this.$parent.$parent.level1done();
          }, 6000);
         
        }
        if(this.firstText==="乐乐回来了！赶紧收好回忆录！"){
         this.$parent.$parent.changescene("livingroom");
         this.$parent.hideDlg=false;
         this.$parent.showmom=false;
         this.$parent.showson=true;
         this.$parent.dlgTexts=["妈妈，生日快乐！",
         "（哦，今天原来是我的生日啊……）",
         "你今天不是去和同学看电影吗？不一起吃饭？",
         "反正都放假了，下次还可以再约。但今天是妈妈生日啊！",
         "我们以前就约好了，生日都要一起过！","妈妈，许个愿吧！",
         "……",
         "(上天啊，求你慢点收走我的记忆，让我再陪乐乐久一点吧。)","(晚一点说出那句“你是谁”吧。)"];
        }
        if(this.firstText==="妈妈，生日快乐！"){
         this.$parent.level3done();
        }
        
      } else {
        
        if(item.value==="乐乐发现门上有一张便条，上面写着：乐乐，妈妈今晚临时加班。我在冰箱里放了吃的，饿了就自己解决晚饭吧。"){
          this.$parent.showson=false;
        }else if(item.value==="坏妈妈，今天是我的生日啊，明明说好一起过的。"){
          this.$parent.showson=true;
          this.turnblack=true;
        }else if(item.value==="我还真忘了这件事！先去喂了吧……"){
          this.$parent.showson=false;
          this.$parent.showmom=true;
        }else if(item.value==="呀，乐乐，你现在才解开密码吗？"||
        item.value==="（苦笑）突然加班，妈妈也没办法啊……抱歉啦，让乐乐久等了。一起吃生日蛋糕吧？"||
        item.value==="乐乐你看，这个布丁是用你喜欢的QQ糖做出来的哦。"||
        item.value==="是啊，它们在仰望星空……"){
          this.$parent.showmom=true;this.$parent.showson=false;
        }else if(item.value==="这是什么？鱼吗？"||
        item.value==="哼，都怪妈妈 ，明知今天是我生日还不陪我！"){
          this.$parent.showmom=false;this.$parent.showson=true;
          }else if(item.value==="呀，这是什么……？"||item.value==="（哽咽）没事……妈妈只是太高兴了。乐乐真棒。"){
            this.$parent.showmom=true;
          }else if(item.value==="妈妈你看！我把零食放在了一起，然后它们就会生出好多好多零食孩子，像小卖部那么多了！"
          ||item.value==="（兴高采烈）我听说……"){
            this.$parent.showmom=false;
          }else if(item.value==="（哦，今天原来是我的生日啊……）"||item.value==="……"){
            this.$parent.showmom=true;
            this.$parent.showson=false;
          }else if(item.value==="反正都放假了，下次还可以再约。但今天是妈妈生日啊！"){
            this.$parent.showmom=false;
            this.$parent.showson=true;
          }
          this.text = item.value;
      }
    }
  },
  created() {}
};
</script>

<style scoped>
.modal{
  position: absolute;
  width: 100%;
  height: 270px;
  bottom: 0;
  background-image: url("/static/1_0/dialogbg.png");
}

p {
  font-size: 50px;
  font-family: "myfont";
  color: white;
  line-height: 70px;
  /*-webkit-text-stroke: 0.5px #ffffff;*/
  /*text-shadow: 0px 0px 0px #ffffff;*/
}
.txt {
  margin-left: 390px;
  width: 1400px;
  margin-top: 65px;
  cursor: pointer;
}
</style>