<template>
  <div @click="change">
    <!--背景图片-->
    <!--
    <div v-for="img in imgs" :key="img.name">
      <transition name="slide-fade">
        <img :src="img.src" v-show="img.name==scene.name"/>
      </transition>
    </div>-->
    <audio id="bluecup" src="/static/第一关/蓝色杯子.wav" hidden="true"></audio>
    <audio id="openmicro" src="/static/第一关/开微波炉.mp3" hidden="true"></audio>
    <audio id="closemicro" src="/static/第一关/关微波炉.mp3" hidden="true"></audio>
    <audio id="microfire" src="/static/第一关/微波炉加热.mp3" hidden="true"></audio>
    <audio id="openegg" src="/static/第一关/打开奇趣蛋.mp3" hidden="true"></audio>
    <audio id="opentv" src="/static/第一关/开电视柜.mp3" hidden="true"></audio>
    <audio id="drink" src="/static/第一关/喝水.wav" hidden="true"></audio>
    <audio id="put" src="/static/第一关/倒饮料.wav" hidden="true"></audio>
    <audio id="paopaotang" src="/static/第一关/吹泡泡糖.mp3" hidden="true"></audio>

    <div class="container">
      <!--博物馆入口-->
      <img v-if="scene.name=='start11'&&showentry1" src="static/museum/entry1.png" class="roomentry">

      <!--可近景物品-->
      <inner-obj
        v-for="obj in objs"
        :key="obj.name"
        :obj="obj"
        :link="obj.link"
        :name="obj.name"
        class="entry"
      ></inner-obj>

      <img
        id="pid"
        src="/static/1_2/p.png"
        v-if="showp"
        :class="{'myp' : panim}"
        style="position:absolute;top:228px;left:1340px;width:144px;height:144px;"
      >
      <img
        src="/static/1_2/p.png"
        v-if="showp"
        :class="{'myp' : panim}"
        style="position:absolute;top:414px;left:1340px;width:144px;height:144px;"
      >

      <!--可被作用物品，不可收集-->
      <inner-obj
        v-for="obj in objs3"
        :key="obj.name"
        :obj="obj"
        :partner="obj.partner"
        :name="obj.name"
        class="cantget"
      ></inner-obj>

      <!--可收集物品-->
      <div v-for="obj in objs2" :key="obj.name">
        <inner-obj v-if="!obj.isget" :obj="obj" @click.native="getobj2($event,obj)"></inner-obj>
      </div>

      <!--可触发对话框对象-->
      <div v-for="obj in oneSceneIns" :key="obj.name">
        <inner-obj :obj="obj" @click.native="hideDialog(obj)"></inner-obj>
      </div>
      <!--其他-->
      <password v-if="scene.name=='tvclose'"></password>
      <img src="/static/1_1/miqi.png" v-show="showmiqi&&scene.name=='livingroom'" class="miqi">
      <img src="/static/1_1/cat.png" v-show="showcat&&scene.name=='livingroom'" class="cat">
      <div class="cat1" v-show="showcat1&&scene.name=='kitchen'"></div>
      <img v-show="showthink&&scene.name=='kitchen'" class="think" src="/static/1_2/think.png">
      <img class="cat2" v-show="showcat2&&scene.name=='kitchen'" src="/static/1_2/cat2.png">

      <img
        src="/static/1_2/microfire.png"
        v-if="showfire"
        class="myfire"
        style="position:absolute;top:221px;left:586px;width:636px;height:444px;"
      >
      <!--桌纸米老鼠飞-->
      <img
        src="/static/1_1/dmiqifly.png"
        class="dmiqifly"
        v-show="showdmiqifly&&scene.name=='apaper'"
      >
      <img
        src="/static/1_1/dshadow.png"
        class="dshadow"
        v-show="showdmiqifly&&scene.name=='apaper'"
      >
      <!--客厅米老鼠飞-->
      <img
        src="/static/1_1/lmiqifly.png"
        class="lmiqifly"
        v-show="showlmiqifly&&scene.name=='livingroom'"
      >
      <img
        src="/static/1_1/lshadow.png"
        class="lshadow"
        v-show="showlmiqifly&&scene.name=='livingroom'"
      >

      <!--<div v-if="showpclick" :class="{'myp' : panim,'myp2':true}"
      style="position:absolute;top:228px;left:1340px;width:144px;height:144px;"></div>
      <div v-if="showpclick" :class="{'myp' : panim,'myp2':true}"
      style="position:absolute;top:414px;left:1340px;width:144px;height:144px;"></div>-->

      <!--场景切换箭头-->
      <img src="/static/museum/down2.png" class="down" v-if="scene.down">
      <img src="/static/museum/right2.png" class="right" v-if="scene.right">
      <img src="/static/museum/right2.png" class="left" v-if="scene.left">
    </div>

    <!--特写图，有阅读按钮-->
    <feature class="fea" v-if="feaCircular==true"></feature>

    <ten-why class="fea" v-if="feaWhy==true">
      <inner-obj
        v-if="!canGetObjs[10].isget&&canGetObjs[10].from=='why'"
        :obj="canGetObjs[10]"
        class="obj10"
      ></inner-obj>
      <inner-obj
        v-if="!canGetObjs[11].isget&&canGetObjs[10].from=='why'"
        :obj="canGetObjs[11]"
        class="obj11"
      ></inner-obj>
    </ten-why>

    <note-book class="fea" v-show="feaNoteBook==true"></note-book>

    <!--对话框-->
    <div class="dialog" v-if="!hideDlg">
      <my-dialog :texts="dlgTexts"></my-dialog>
      <img v-if="showmom" src="/static/1_0/mom.png" width="330px" height="540px" style="left:0">
      <img
        v-else-if="showson2"
        src="/static/museum/son1.png"
        width="360px"
        height="660px"
        style="left:0px"
      >
      <img v-else src="/static/1_0/son.png" width="258px" height="414px" style="left:40px">
    </div>

    <img src="/static/enlarge/small.png" class="endsmall" v-if="showend1">

    <div class="unclickable" v-if="unclickable"></div>
  </div>
</template>

<script>
import InnerObj from "./InnerObj.vue";
import MyDialog from "./MyDialog.vue";
import Password from "./Password.vue";
//import Collection from "./Collection.vue";
import Feature from "./Feature.vue";
import TenWhy from "./TenWhy.vue";
import NoteBook from "./NoteBook.vue";

//import axios from "axios";

export default {
  name: "SceneOne",
  components: { InnerObj, MyDialog, Password, Feature, TenWhy, NoteBook },
  props: ["scene"],
  data: function() {
    return {
      //scene: this.scene,
      hideDlg: true,
      haveBag: false,
      dlgTexts: [],
      feaCircular: false,
      feaWhy: false,
      feaNoteBook: false,
      showmiqi: false,
      showcat: false,
      showcat1: true,
      showcat2: false,
      showthink: false,
      showmom: false,
      showson2: false,
      showp: false,
      showfire: false,
      panim: false,
      showdmiqifly: false,
      showlmiqifly: false,
      showentry1:false,
      showend1:false,
      unclickable:false,
      //getmiqifly:false,
      linkobjs: [],

      canGetObjs: [],

      cantGetObjs: [],

      instru: []
    };
  },
  computed: {
    objs: function() {
      let that = this;
      return this.linkobjs.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    objs2: function() {
      let that = this;
      return this.canGetObjs.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    oneSceneIns: function() {
      let that = this;
      return this.instru.filter(function(item) {
        return item.from === that.scene.name;
      });
    },
    objs3: function() {
      let that = this;
      return this.cantGetObjs.filter(function(item) {
        return item.from === that.scene.name;
      });
    }
  },
  methods: {
    change(e) {
      if (e.target.className == "up") {
        this.$parent.changescene(this.scene.up);
      } else if (e.target.className == "down") {
        this.$parent.changescene(this.scene.down);
        //document.getElementById("btn").play();

        /**微波炉部分 */
        if (this.scene.name === "amicro" || this.scene.name === "bmicro") {
          //微波炉退出时隐藏指针
          this.showp = false;
        }
        /** */
      } else if (e.target.className == "left") {
        this.$parent.changescene(this.scene.left);
        //document.getElementById("btn").play();
      } else if (e.target.className == "right") {
        this.$parent.changescene(this.scene.right);
        //document.getElementById("btn").play();
      } else if (e.target.className == "roomentry") {
        this.$parent.$parent.showturn=true;
        let that=this;
        setTimeout(function(){
          that.$parent.changescene("livingroom");
        that.feaNoteBook = true;
        that.showson2 = false;
        that.hideDlg=false;
        that.dlgTexts = [
          "今天是爸爸生日，但是他又没遵守约定回家，还让妈妈伤心了，大坏蛋！",
          "是谁说男子汉不可以惹妈妈不高兴的！",
          "以前都是我们一起去逗妈妈开心的，不过……看在爸爸给我带了好多我喜欢的零食的份上，我就帮爸爸把妈妈哄回来好了。",
          "等下次见到爸爸，就告诉他我才是男子汉！",
          "对了，今天听小明说，爸爸和妈妈在一起就会生小孩，才会有我。",
          "那零食和零食在一起也会生小孩吗？小卖部里那么多零食，会不会都是好多零食在一起生出来的？",
          "那我要用爸爸给我的零食像小卖部那样摆起来，这样它们就会生孩子，以后我和妈妈就可以天天吃零食啦。",
          "等爸爸回来，我们就能一起吃了！",
          "唔，学校旁边新开的那家小卖部，零食摆得像彩虹一样。正好我给爸爸画的画还差彩虹，就放在画上一起给妈妈吧！",
          "小明还在那里见过会飞的老鼠呢！如果我做小卖部老板，我也要一只小飞鼠做店员！超帅！"
        ];
        document.getElementById("level1").play();
        document.getElementById("before1").currentTime = 0;
        document.getElementById("before1").pause();
        },2000);
        
      } else if (e.target.className == "entry") {
        let name = e.target.getAttribute("name");
        if (name === "emptyredbag") {
          this.linkobjs[3].from = "";
          this.linkobjs[5].from = "livingroom";
          this.linkobjs[6].from = "afterclick";
        } else if (name === "microdoor2") {
          document.getElementById("openmicro").play();
          /**微波炉部分 */
          this.canGetObjs[12].from = "bmicro"; //开门，出现有好多鱼的容器

          //this.linkobjs[8].link = "amicro"; //把门关到普通状态
          //this.linkobjs[0].link = "amicro"; //把远景的微波炉链接到普通状态
        } else if (name === "micro") {
          this.showp = true; //显示指针
        } else if (name === "pwok") {
          document.getElementById("pwright").play();
        } else if (name === "openmicrodoor") {
          document.getElementById("closemicro").play();
        } else if (name === "microdoor") {
          document.getElementById("openmicro").play();
        }
        /** */

        this.$parent.changescene(e.target.getAttribute("link"));
      } else if (e.target.className == "cantget") {
        let partner = e.target.getAttribute("partner");
        let name = e.target.getAttribute("name");
        if (partner) {
          if (this.$parent.activeItem === partner) {
            if (partner === "miqi" && this.$parent.getmiqifly) {
              this.showdmiqifly = true;
              this.showlmiqifly = true;
              this.$parent.removeItem(partner);
              let that = this;
              setTimeout(function() {
                that.showmom=false;
                that.showson2=false;
                that.hideDlg = false;
                that.dlgTexts = ["叫妈妈来看吧！"];
              }, 1500);
              return;
            } else if (partner === "miqi" && !this.$parent.getmiqifly) {
              this.$parent.activeItem = null;
              return;
            }

            this.$parent.removeItem(partner);

            if (name == "cmat") {
              this.cantGetObjs[5].from = "amat";
              this.linkobjs[9].from = "livingroom";
            } else if (name == "cuponmat") {
              if (partner === "fishnet") this.cantGetObjs[8].from = "amat";
              //出现渔网
              else if (partner === "whitechild") {
                this.cantGetObjs[8].from = "amat"; //出现渔网
                this.cantGetObjs[11].from = "amat"; //出现透明颗粒

                //图片预加载
    new Image().src = "/static/1_1/fishchild.png";
              }
            } else if (name == "afishnet") {
              this.canGetObjs[13].from = "amat"; //放上透明颗粒
              this.cantGetObjs[6].from = "amat"; //杯里出现隐雪
              document.getElementById("put").play();
            } else if (name == "whitechild2") {
              this.cantGetObjs[7].from = "amat"; //出现橙汁
              this.cantGetObjs[11].from = ""; //拿走透明颗粒
              this.canGetObjs[14].from = "amat"; //出现橙色鱼籽
              document.getElementById("put").play();
            } else if (name == "emptyctn") {
              /**微波炉部分 */
              this.cantGetObjs[10].from = "bmicro"; //出现有橙色鱼籽的容器
              //this.linkobjs[8].link = "microfire"; //把门关到加热状态
              this.cantGetObjs[12].from = "amicro"; //指针可以点击
              this.cantGetObjs[13].from = "amicro";
            }
          } else {
            this.$parent.activeItem = null;
          }
        }
        if (name === "cabinet") {
          this.canGetObjs[3].from = "livingroom";
          this.canGetObjs[4].from = "livingroom";
          var close = document.getElementById("close");
          close.play();
        } else if (name === "pwok") {
          var opentv = document.getElementById("opentv");
          opentv.play();
        } else if (name === "circular") {
          this.feaCircular = true;
        } else if (name === "why") {
          this.feaWhy = true;
        } else if (name === "notebook") {
          this.feaNoteBook = true;
        } else if (name === "snowincup") {
          this.cantGetObjs[6].from = ""; //移走盛隐雪的杯子
          this.cantGetObjs[5].partner = "whitechild"; //空杯子搭配透明颗粒
          var drink = document.getElementById("drink");
          drink.play();
        } else if (name === "pclick" || name === "pclick2") {
          this.panim = true; //播放指针动画
          this.showfire = true; //播放渐变
          document.getElementById("microfire").play();
          var myp = document.getElementById("pid");
          myp.addEventListener("webkitAnimationEnd", this.mypfunc);
        } else if (name === "dcolor") {
          if (this.$parent.activeItem === "redQQ") {
            this.cantGetObjs[14].from = "apaper";
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "orangeQQ") {
            this.cantGetObjs[15].from = "apaper";
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "yellowQQ") {
            this.cantGetObjs[16].from = "apaper";
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "greenQQ") {
            this.cantGetObjs[17].from = "apaper";
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "blueQQ") {
            this.cantGetObjs[18].from = "apaper";
            this.$parent.removeItem(this.$parent.activeItem);
          } else if (this.$parent.activeItem === "miqi") {
            this.$parent.activeItem = null;
          }
          if (
            this.cantGetObjs[14].from == "apaper" &&
            this.cantGetObjs[15].from == "apaper" &&
            this.cantGetObjs[16].from == "apaper" &&
            this.cantGetObjs[17].from == "apaper" &&
            this.cantGetObjs[18].from == "apaper"
          ) {
            this.cantGetObjs[19].partner = "miqi";
          }
        }
      } else if (e.target.className == "obj10") {
        this.getobj2(e, this.canGetObjs[10]);
      } else if (e.target.className == "obj11") {
        this.getobj2(e, this.canGetObjs[11]);
      } else if (e.target.className == "cat1") {
        if (this.$parent.activeItem === "manyfish") {
          this.$parent.removeItem("manyfish");
          this.showcat1 = false;
          this.showcat2 = true;
          this.showthink = false;
        } else {
          this.showthink = true;
          var think = document.getElementsByClassName("think")[0];
          think.addEventListener("webkitAnimationEnd", this.thinkfunc);
          document.getElementById("miao").play();
          this.$parent.activeItem = null;
        }
      } else if (e.target.className == "miqi") {
        this.unclickable=true;
        document.getElementById("miao").play();
        this.showcat = true;
        this.showmiqi = false;
        var cat = document.getElementsByClassName("cat")[0];
        cat.addEventListener("webkitAnimationEnd", this.catfunc);
      }
    },
    getobj2(event, obj) {
      event.stopPropagation();
      //event.cancelBubble = true;
      if (obj.name == "emptyredbag") {
        //event.stopPropagation();
        obj.isget = true;
        return;
      } else if (obj.name == "cabinetopen") {
        //event.stopPropagation();
        obj.from = "";
        var open = document.getElementById("open");
        open.play();
        if (this.canGetObjs[4].isget === false) {
          this.canGetObjs[4].from = "";
        }
        return;
      }

      if (this.haveBag) {
        obj.isget = true;
        let item = {};
        item.name = obj.name;
        item.partner = obj.partner;
        item.bagimg = obj.bagimg;
        item.text = obj.text;
        item.show = true;
        this.$parent.addItem(item);

        //window.event? window.event.cancelBubble = true : e.stopPropagation();
        if (
          this.canGetObjs[5].isget &&
          this.canGetObjs[6].isget &&
          this.canGetObjs[7].isget
        ) {
          this.linkobjs[4].from = "aredbag";
        }
        if (obj.name == "whitechild" || obj.name == "fishchild") {
          this.cantGetObjs[8].from = ""; //移走渔网
        } else if (obj.name == "manyfish") {
          this.linkobjs[1].name = "microdoor";
        } else if (obj.name == "bluecup") {
          var bluecup = document.getElementById("bluecup");
          bluecup.play();
        }
      } else {
        //event.stopPropagation();   //不会被收集到背包栏
        if (obj.name == "egg") {
          this.haveBag = true;
          obj.isget = true;
          this.showmiqi = true;
          this.$parent.havebag = true;
          //var miqi = document.getElementsByClassName("miqi")[0];
          //miqi.addEventListener("webkitAnimationEnd", this.miqifunc);
          var openegg = document.getElementById("openegg");
          openegg.play();

          //图片预加载
    new Image().src = "/static/1_0/squareon.png";
    new Image().src = "/static/1_0/featurectn.png";
        } else {
          this.hideDlg = false;
          this.showmom = false;
          this.dlgTexts = ["我没有口袋，先找个能装东西的吧。"];
        }
      }
    },
    hideDialog(obj) {
      this.hideDlg = false;
      this.dlgTexts = obj.text;
      if (obj.name == "circularsay") {
        obj.from = "";
        this.feaCircular = true;
      }
      if (obj.name == "mom") {
        this.showmom = true;
      } else {
        this.showmom = false;
      }
    },
    catfunc() {
      this.showcat = false;
      this.hideDlg = false;
      this.unclickable=false;
      this.showmom = false;
      this.dlgTexts = ["哎呀！我的老鼠店员！"];
      this.canGetObjs[15].from = "kitchen";

      //var miqi = document.getElementsByClassName("miqi")[0];
      //miqi.removeEventListener("webkitAnimationEnd", this.miqifunc);
      var cat = document.getElementsByClassName("cat")[0];
      cat.removeEventListener("webkitAnimationEnd", this.catfunc);
    },
    mypfunc() {
      this.panim = false;
      this.showfire = false;
      this.cantGetObjs[12].from = ""; //指针点击区域移走
      this.cantGetObjs[13].from = "";
      this.cantGetObjs[10].from = ""; //拿走有橙色鱼籽的容器
      var myp = document.getElementById("pid");
      myp.removeEventListener("webkitAnimationEnd", this.mypfunc);
      this.linkobjs[1].name = "microdoor2";
    },
    thinkfunc() {
      this.showthink = false;
      var think = document.getElementsByClassName("think")[0];
      think.removeEventListener("webkitAnimationEnd", this.thinkfunc);
    },
    getData() {
      this.axios.get("/R.json").then(res => {
        this.linkobjs = res.data.linkobjs;
        this.canGetObjs = res.data.canGetObjs;
        this.cantGetObjs = res.data.cantGetObjs;
        this.instru = res.data.instru;
      });
    }
  },
  mounted() {
    /*bus.$on("updatestatus", name => {
      if (name === "miqi") {
        this.getmiqifly = true;
      }
    });*/
    this.showson2 = true;
    this.hideDlg = false;
    this.dlgTexts = [
      "啊，这是妈妈给我过生日时拍的照片。",
      "说起来，这本回忆录就是在这个生日开始做的，我和妈妈一起写了第一页。",
      "虽然说过一起做这本本子，但我后来嫌麻烦懒得管了。这么多年来还以为不见了呢，没想到妈妈一直保存得这么好。",
      "那时候我好小啊，我都记不清当时自己写什么了。"
    ];

    //图片预加载
    
    new Image().src = "/static/1_0/son.png";
  },
  created() {
    this.getData();
  },
  updated(){
  }
};
</script>

<style scoped>
.container {
  position: absolute;
  width: 100%;
  height: 75%;
}


.dialog {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  /*background-color: rgba(0, 0, 0, 0.5);*/
}
.dialog img {
  position: absolute;
  bottom: 0;
}
.fea {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.unclickable {
  position: absolute;
