<template>
  <div class="ctn" @click="eventslisten">
    <img
      v-show="state===1"
      class="last"
      src="/static/2_1/last.png"
      width="720px"
      height="72px"
      style="top:5px;left:578px"
    >
    <img src="/static/2_1/acalendar.png" style="width:930px;height:774px;top:30px;left:460px">
    <img
      v-show="state===0"
      src="/static/2_1/march.png"
      style="width:690px;height:510px;top:145px;left:580px"
    >
    <img
      class="read"
      v-show="state===0"
      src="/static/1_0/read.png"
      style=" position: absolute; top: 525px; left: 930px;
  width:42px;height:42px"
    >
    <img
      v-show="state===1"
      src="/static/2_1/april.png"
      width="690px"
      height="510px"
      style="top:145px;left:580px"
    >
    <img
      v-show="state===0&&getnote4"
      class="next"
      src="/static/2_1/next.png"
      width="144px"
      height="156px"
      style="top:540px;left:1180px"
    >
    <slot></slot>
    <img src="/static/museum/down2.png" class="mydown">
    <div class="moreinfo" v-show="reading">
      <p>3月21日，“今天是乐乐的生日”</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Calendar",
  components: {},
  props: ["getnote4"],
  data: function() {
    return {
      reading: false,
      state: 0
    };
  },
  computed: {},
  methods: {
    eventslisten(e) {
      if (e.target.className == "read") {
        this.reading = true;
        document.getElementById("btn").play();
        this.$parent.hideDlg = false;
        this.$parent.dlgTexts = ["原来妈妈记得今天是我生日啊……"];
      } else if (e.target.className == "mydown") {
        this.$off();
        this.$parent.feaCal = false;
        this.$parent.canGetObjs[9].from = "";
        //document.getElementById("btn").play();
      } else if (e.target.className == "moreinfo") {
        this.reading = false;
      } else if (e.target.className == "next") {
        this.state = 1;
        this.$parent.canGetObjs[9].from = "cal";
      } else if (e.target.className == "last") {
        this.state = 0;
        this.$parent.canGetObjs[9].from = "";
      }
    }
  },
  mounted() {}
};
</script>

<style scoped lang="scss">
.ctn {
  background-color: rgba(0, 0, 0, 0.4);
}
img {
  position: absolute;
}
.read {
  cursor: pointer;
}
.mydown {
  position: absolute;
  width: 70px;
  height: 40px;
  left: 49%;
  top: 73%;
  z-index: 8;
  cursor: pointer;
}
.moreinfo {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 8;
  background-color: rgba(0, 0, 0, 0.8);
  padding-top: 300px;
  padding-left: 600px;
  cursor: pointer;
}

p {
  color: white;
  margin-top: 40px;
  font-family: "myfont";
  font-weight: normal;
  font-size: 40px;
}
</style>