<template>
  <div class="ctn">
    <img
      v-show="state===0"
      src="/static/1_1/whyclose.png"
      width="684px"
      height="564px"
      style="top:103px;left:670px"
    >
    <img
      v-show="state===1"
      src="/static/1_1/whywrong.png"
      width="924px"
      height="642px"
      style="top:25px;left:430px"
    >
    <img
      v-show="state===2"
      src="/static/1_1/whyinfo.png"
      width="924px"
      height="642px"
      style="top:25px;left:430px"
    >
    <img
      v-show="state===3"
      src="/static/1_1/whyopen.png"
      width="864px"
      height="642px"
      style="top:25px;left:380px"
    >

    <div class="numdiv" @click="add">
      <div class="numdiv1" style="top:198px;left:1175px;">{{num1}}</div>
      <div class="numdiv2" style="top:198px;left:1245px;">{{num2}}</div>
      <div class="okbtn" style="top:265px;left:1290px;width：50px;height:30px;"></div>

      <img
        class="myleft"
        v-show="state===3"
        src="/static/1_1/whyleft.png"
        width="54px"
        height="84px"
        style="top:250px;left:360px"
      >
      <img
        class="myright"
        v-show="state===2"
        src="/static/1_1/whyright.png"
        width="60px"
        height="84px"
        style="top:320px;left:1270px"
      >
      <img src="/static/museum/down2.png" class="mydown">

      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "TenWhy",
  components: {},
  props: [],
  data: function() {
    return {
      num1: 0,
      num2: 0,
      state: 0
    };
  },
  computed: {},
  methods: {
    add: function(e) {
      if (e.target.className == "numdiv1") {
        this.num1 = (this.num1 + 1) % 10;
        document.getElementById("password").play();
      } else if (e.target.className == "numdiv2") {
        this.num2 = (this.num2 + 1) % 10;
        document.getElementById("password").play();
      } else if (e.target.className == "myright") {
        this.state = 3;
        this.$parent.canGetObjs[10].from = "why";
        this.$parent.canGetObjs[11].from = "why";
        document.getElementById("btn").play();
      } else if (e.target.className == "myleft") {
        this.state = 2;
        this.$parent.canGetObjs[10].from = "";
        this.$parent.canGetObjs[11].from = "";
        document.getElementById("btn").play();
      } else if (e.target.className == "mydown") {
        this.$off();
        this.$parent.feaWhy = false;
        this.$parent.canGetObjs[10].from = "";
        this.$parent.canGetObjs[11].from = "";
        //document.getElementById("btn").play();
      } else if (e.target.className == "okbtn") {
        if (this.num1 == 8 && this.num2 == 7) {
          this.state = 2;

          //图片预加载
          new Image().src = "/static/1_1/blueQQ.png";
          new Image().src = "/static/1_1/greenQQ.png";
        } else {
          this.state = 1;
        }
      }
    }
  }
};
</script>

<style scoped lang="scss">
.numdiv {
  cursor: pointer;
}
.numdiv1,
.numdiv2,
.okbtn {
  position: absolute;
  width: 55px;
  height: 63px;
  text-align: center;
  font-size: 64px;
  -webkit-transform: skewX(26deg);
  transform: skewX(26deg);
  /*border: 1px solid #000;*/
}
.ctn {
  background-color: rgba(0, 0, 0, 0.8);
}
.ctn img {
  position: absolute;
}
.mydown {
  position: absolute;
  width: 70px;
  height: 40px;
  left: 49%;
  top: 68%;
  cursor: pointer;
}
</style>
